//Flexbox Grid
.df-fb {
  @extend %flexbox !optional;
}

.df-borRad100 {
  @include border-radius(100px);
}

.df-borRad12 {
  @include border-radius(12px);
}

.df-borRad8 {
  @include border-radius(8px);
}

.df-borRad6 {
  @include border-radius(6px);
}

.df-borRad4 {
  @include border-radius(4px);
}

.df-borRad2 {
  @include border-radius(2px);
}

.df-borRad0 {
  @include border-radius(0px);
}

// Box Shadow
.df-boxShadow--sm {
  @include box-shadow(0, 2px, 4px, 0, rgba(1, 1, 6, 0.05));
}

// font-family
.df-lightFF {
  font-family: $light_font-family;
  font-weight: $light_font-weight;
}

.df-lightFF--it {
  font-family: $light_font-family-it;
  font-weight: $light_font-weight;
}

.df-regularFF {
  font-family: $regular_font-family;
  font-weight: $regular_font-weight;
}

.df-regularFF--it {
  font-family: $regular_font-family-it;
  font-weight: $regular_font-weight;
}

.df-mediumFF {
  font-family: $medium_font-family;
  font-weight: $medium_font-weight;
}

.df-mediumFF--it {
  font-family: $medium_font-family-it;
  font-weight: $medium_font-weight;
}

.df-boldFF {
  font-family: $bold_font-family;
  font-weight: $bold_font-weight;
}

.df-blackBoldFF {
  font-family: $black_font-family;
  font-weight: $black_font-weight;
}

.df-boldFF--it {
  font-family: $bold_font-family-it;
  font-weight: $bold_font-weight;
}

// secondary font-family
.df-mediumFF--sec {
  font-family: $medium_font-family-sec;
  font-weight: $medium_font-weight-sec;
}

.df-boldFF--sec {
  font-family: $bold_font-family-sec;
  font-weight: $bold_font-weight-sec;
}

.df-blackFF--sec {
  font-family: $black_font-family-sec;
  font-weight: $black_font-weight-sec;
}

//background size
.df-bgSize--cover {
  @include background-size(cover);
}

.df-bgSize--contain {
  @include background-size(contain);
}
