// Pick Player List
.df-plyrSel {
  &__wrap {
    overflow: hidden;
    background: $white;

    .df-tbl {
      padding: 0 16px;
      width: 100%;
      table-layout: fixed;

      &__head {
        .df-tbl__cell {
          font-size: 12px;
          color: rgba($black, 0.6);
          @extend .df-regularFF !optional;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 14px;
          }

          &--thumb,
          &--name,
          &--pts,
          &--amt,
          &--captn {
            cursor: pointer;
          }

          &.df-active {
            &--up {
              span {
                &::after {
                  @include transform(translateY(-50%) rotateX(180deg));
                }
              }
            }

            &--dw {
              span {
                &::after {
                  @include transform(translateY(-50%));
                }
              }
            }

            &--up,
            &--dw {
              span {
                display: inline-block;
                color: $client-primary-color;
                position: relative;

                &::after {
                  content: "\e908";
                  font-family: "dfi" !important;
                  color: $red;
                  position: absolute;
                  left: auto;
                  right: -12px;
                  top: 50%;
                  width: 12px;
                  height: 12px;
                  font-size: 10px;
                  justify-content: center;
                  align-items: center;
                  @extend %flexbox !optional;
                }
              }
            }
          }
        }
      }

      &__body {
        .df-tbl__cell {
          &--pts {
            font-size: 14px;
            color: rgba($text-dark, 0.6);
            @extend .df-boldFF !optional;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 16px;
            }
          }

          &--amt {
            padding-right: 18px;
            white-space: nowrap;
            font-size: 14px;
            text-align: left;
            color: $text-dark;
            @extend .df-boldFF !optional;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 16px;
            }
          }

          &--captn {
            font-size: 12px;
            color: rgba($text-dark, 0.6);
            vertical-align: top;
            padding-top: 4px;
            @extend .df-regularFF !optional;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 14px;
            }
          }
        }
      }

      &__cell {
        padding: 0 4px;

        &--thumb {
          width: 72px;
          position: relative;
          cursor: pointer;

          @media screen and (min-width: $media-mobile-max) {
            width: 80px;
          }
        }

        &--name {
          width: 100%;
        }

        &--pts {
          width: 70px;
          padding-right: 24px;
          text-align: right;

          @media screen and (min-width: $media-mobile-max) {
            width: 80px;
          }
        }

        &--amt {
          width: 60px;

          @media screen and (min-width: $media-mobile-max) {
            width: 52px;
          }
        }

        &--captn {
          width: 60px;
          text-align: center;

          @media screen and (min-width: $media-mobile-max) {
            width: 92px;
          }
        }

        &--cta {
          width: 24px;

          @media screen and (min-width: $media-mobile-max) {
            width: 32px;
          }
        }
      }
    }

    &--prizes {
      background-color: transparent;

      .df-noDataFound {
        padding: 30px 0;

        &__text {
          padding-bottom: 40px;
          padding-left: 16px;
          padding-right: 16px;
          span {
            display: block;
            font-size: 14px;
            color: $black;
            b {
              font-size: 14px;
              color: $client-primary-color;
            }
          }
        }
      }

      .df-plyrSel__list,
      .df-plyrSel__top {
        background-color: $white;
      }

      .df-plyrSel__list {
        padding-bottom: 0;

        .df-tbl__row {
          &:not(:last-child) {
            .df-tbl__cell {
              border-bottom: 1px solid #f5f5f5;
            }
          }
        }

        .df-tbl__cell {
          padding-top: 22px;
          padding-bottom: 22px;

          &--name,
          &--pts {
            @extend .df-mediumFF !optional;
            font-size: 14px;
            color: $client-primary-color;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 16px;
            }
          }
        }
      }

      .df-tbl__cell--pts {
        padding-right: 4px;
      }
    }
  }

  &-newJoinee {
    background: transparent;

    .df-plyrSel__list {
      &.df-CompareTeam {
        ul li {
          background: $duck-egg-blue-two !important;
          border-color: rgba(#dcdcdc, 0.6) !important;
          opacity: 0.4;
        }
      }

      ul li {
        cursor: default;
      }
    }
  }

  &__top {
    padding: 8px 0;
    border-bottom: 1px solid rgba(#dcdcdc, 0.6);
    // &--fixed {
    //   position: fixed;
    //   background: $white;
    //   z-index: 1;
    //   max-width: 550px;
    //   width: 100%;
    // }
  }

  &__list {
    background: $duck-egg-blue;
    overflow-y: auto;
    padding-bottom: 70px;

    &-title {
      padding: 12px 16px 0;
      color: $client-primary-color;
      font-size: 20px;
      @extend .df-boldFF !optional;
    }

    .df-transfer__tbl .df-tbl {
      padding: 0;
    }

    .df-accordion {
      &__head {
        padding-bottom: 0;

        .df-transfer__fix ul li:not(:last-child) {
          border-bottom: none;
        }
      }
    }

    .df-noDataFound {
      padding: 32px 0;
    }

    ul {
      li {
        background: $white;
        padding: 8px 0;
        cursor: pointer;

        &:not(:last-child) {
          border-bottom: 1px solid rgba(#dcdcdc, 0.6);
        }
        .df-prize__header {
          min-width: 100%;
          @media screen and (min-width: $media-mobile-max) {
            min-width: 80%;
          }
          &-text {
            &--2 {
              color: $client-primary-color;
            }
          }
        }
        &.df-remove {
          background: $pale;

          .df-plyrSel__cta {
            color: $status;
            border-color: $status;

            &::before,
            &::after {
              background: $status;
            }

            &::after {
              height: 0;
            }
          }
        }

        &.df-unavailable {
          // .df-plyrSel__name {
          //   &::after {
          //     content: "";
          //     width: 12px;
          //     height: 12px;
          //     background: url(../images/unavailable.svg?v=#{$image-version}) center center no-repeat;
          //     position: absolute;
          //     left: auto;
          //     right: 0;
          //     top: 50%;
          //     @include transform(translateY(-50%));
          //     @include background-size(contain);
          //   }
          // }
        }

        &.df-injured {
          // .df-plyrSel__name {
          //   &::after {
          //     content: "";
          //     width: 12px;
          //     height: 12px;
          //     background: url(../images/injured.svg?v=#{$image-version}) center center no-repeat;
          //     position: absolute;
          //     left: auto;
          //     right: 0;
          //     top: 50%;
          //     @include transform(translateY(-50%));
          //     @include background-size(contain);
          //   }
          // }
        }

        &.df-disabled {
          position: relative;
          // pointer-events: none;
          cursor: default;

          &::after {
            content: "";
            pointer-events: none;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba($white, 0.4);
          }

          > div {
            opacity: 0.4;
          }

          .df-plyrSel__cta {
            border-color: rgb(163, 176, 208);

            &::before,
            &::after {
              background: rgb(163, 176, 208);
            }
          }
        }

        &.df-injured,
        &.df-unavailable,
        &.df-banned,
        &.df-in,
        &.df-out {
          .df-plyrSel__choice {
            position: relative;
            padding-left: 10px;
            @extend %flexbox !optional;

            &::after {
              content: "";
              position: absolute;
              left: 0;
              top: 50%;
              @include transform(translateY(-50%));
            }
          }
        }

        &.df-in,
        &.df-out {
          .df-plyrSel__choice {
            &::after {
              width: 6px;
              height: 6px;
              @extend .df-borRad100 !optional;
            }
          }
        }

        &.df-in {
          .df-plyrSel__choice {
            color: $status-green;

            &::after {
              background: $status-green;
            }
          }
        }

        &.df-out {
          .df-plyrSel__choice {
            color: transparent;

            &::after {
              background: $tomato-red;
            }
          }
        }

        &.df-injured,
        &.df-unavailable,
        &.df-banned {
          .df-plyrSel__choice {
            padding-left: 16px;
            color: $tomato-red;

            &::after {
              width: 12px;
              height: 12px;
              background-position: center center;
              background-repeat: no-repeat;
              @extend .df-bgSize--contain !optional;
              @extend .df-borRad0 !optional;
            }
          }
        }

        &.df-injured {
          .df-plyrSel__choice {
            &::after {
              background-image: url(../images/injured.svg?v=#{$image-version});
            }
          }
        }

        &.df-unavailable {
          .df-plyrSel__choice {
            &::after {
              background-image: url(../images/unavailable.svg?v=#{$image-version});
            }
          }
        }

        &.df-banned {
          // pointer-events: none;
          cursor: default;

          .df-tbl__cell--thumb,
          .df-tbl__cell--pts,
          .df-tbl__cell--amt,
          .df-plyrSel__name {
            opacity: 0.4;
          }

          .df-plyrSel__cta {
            // display: none;
          }

          .df-plyrSel__choice {
            &::after {
              background-image: url(../images/ic-warning.svg?v=#{$image-version});
            }
          }
        }

        &.df-pagination-loader {
          position: relative;
          min-height: 4px * 30;
        }

        &.df-rank {
          &First,
          &Second {
            .df-tbl__cell--amt {
              padding: 0;
              text-align: center;

              em {
                display: none;
              }

              span {
                position: relative;
                height: 40px;
                width: 60px;
                margin-right: 0;
                margin-left: auto;
                justify-content: center;
                align-items: center;
                @extend %flexbox !optional;

                &::after,
                &::before {
                  content: "";
                  position: absolute;
                  left: 0;
                  top: 0;
                  width: 100%;
                  height: 100%;
                  background-repeat: no-repeat;
                  @extend .df-bgSize--contain !optional;
                }

                &::after {
                  background-position: left center;
                }

                &::before {
                  background-position: right center;
                }
              }
            }
          }

          &First {
            .df-tbl__cell--amt {
              span {
                &::after {
                  background-image: url(../images/golden-leaf--lft.svg?v=#{$image-version});
                }

                &::before {
                  background-image: url(../images/golden-leaf--right.svg?v=#{$image-version});
                }
              }
            }
          }

          &Second {
            .df-tbl__cell--amt {
              span {
                &::after {
                  background-image: url(../images/silver-leaf--left.svg?v=#{$image-version});
                }

                &::before {
                  background-image: url(../images/silver-leaf--right.svg?v=#{$image-version});
                }
              }
            }
          }
        }
      }
    }
  }

  &__status {
    position: absolute;
    left: -12px;
    top: auto;
    bottom: -4px;
    cursor: default;
    // pointer-events: none;
    height: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    flex-flow: column;
    @extend %flexbox !optional;

    > div {
      &:not(:last-child) {
        margin-bottom: 4px;
      }
    }
  }

  &__batch {
    overflow: hidden;
    // border: 0.5px solid $light-grey-blue;
    justify-content: flex-start;
    align-items: center;
    @extend .df-borRad100 !optional;
    @include box-shadow(0, 1px, 3px, 0, rgba($black, 0.15));
    @extend %inline-flex;
  }

  &__info {
    width: 14px;
    height: 100%;
    font-size: 14px;
    text-align: center;
    color: #8f8f8f;

    i {
      cursor: pointer;

      &:hover {
        color: $client-primary-color;
      }
    }
  }

  &__flight {
    width: 16px;
    height: 12px;
    font-size: 10px;
    text-align: center;
    color: $light-grey-blue;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
      width: 20px;
    }

    .dfi-uncap {
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-image: url(../images/uncapped.svg?v=#{$image-version});
        background-repeat: no-repeat;
        background-position: center center;
        @extend .df-bgSize--contain !optional;
      }
    }
  }

  &__logo {
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center center;
    @extend .df-bgSize--contain !optional;

    @media screen and (min-width: $media-mobile-max) {
      width: 20px;
      height: 20px;
    }
  }

  &__team,
  &__skill {
    padding: 2px 4px;
    font-size: 10px;
    color: $default;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__team {
    background: $white;
  }

  &__skill {
    background-color: #dadada;
  }

  &__thumb {
    cursor: pointer;
    margin-bottom: -8px;
    height: 64px;
    width: 64px;
    background-position: center bottom;
    background-repeat: no-repeat;
    @extend .df-bgSize--contain !optional;

    @media screen and (min-width: $media-mobile-max) {
      height: 72px;
      width: 72px;
    }
  }

  &__name {
    font-size: 14px;
    @extend .df-boldFF !optional;
    color: $text-dark;
    position: relative;
    display: inline-grid;
    // padding-right: 16px;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }

    span {
      @include text-truncate;
    }
  }

  &__admin-user {
    flex-basis: 100%;
    margin-top: 4px;
    align-items: center;
    justify-content: flex-start;
    font-size: 12px;
    @extend .df-boldFF !optional;
    @extend %inline-flex;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }

    i {
      margin-right: 4px;
    }
  }

  &__stats {
    font-size: 12px;
    color: rgba($text-dark, 0.6);
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }
  }

  &__choice {
    display: none;
    font-size: 10px;
    justify-content: flex-start;
    align-items: center;
    min-height: 15px;
    margin-top: 4px;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__cta {
    width: 20px;
    height: 20px;
    border: 2px solid $status-green;
    position: relative;
    @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      width: 24px;
      height: 24px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      margin: 0 auto;
      background: $status-green;
      @include transform(translateY(-50%));
      @include transition(all 0.1s linear);
      @include border-radius(100px);
    }

    &::before {
      width: calc(100% - 6px);
      height: 2px;
    }

    &::after {
      height: calc(100% - 6px);
      width: 2px;
    }
  }

  &__cap {
    margin: 0 auto 4px;
    background: $white;
    color: rgba($text-dark, 0.3);
    border: 1px solid rgba($black, 0.12);
    width: 36px;
    height: 36px;
    font-size: 14px;
    justify-content: center;
    align-items: center;
    @extend .df-boldFF !optional;
    @extend .df-borRad100 !optional;
    @include transition(all 0.15s ease-in-out);
    @extend %flexbox !optional;

    i {
      font-size: 12px;
    }

    &.df-active {
      border-color: $client-primary-color;
      background: $client-primary-color;
      color: $white;
    }
  }

  &__content {
    padding: 16px;
    background: $duck-egg-blue;
    text-align: center;

    > div {
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    &-thumb {
      width: 48px;
      height: 48px;
      margin: 0 auto 16px;
      background-position: center center;
      background-repeat: no-repeat;
      background-color: $very-light-purple;
      @extend .df-borRad100 !optional;
      @extend .df-bgSize--contain !optional;
    }

    &-txt {
      &One {
        font-size: 14px;
        @extend .df-boldFF !optional;
        color: $client-primary-color;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 16px;
        }
      }

      &Two,
      &Three {
        font-size: 12px;
        @extend .df-regularFF !optional;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }
      }

      &Two {
        color: $steel;

        em {
          font-size: 12px;
          margin: 0 4px;
          padding: 1px 6px;
          border: 1px solid $steel;
          @extend .df-borRad100 !optional;
        }
      }

      &Three {
        color: $client-primary-color;
      }
    }

    &-chkbox {
      margin-bottom: -16px;
      margin-top: 16px;

      .df-checkBox {
        [type="checkbox"] {
          &:checked,
          &:not(:checked) {
            & + label {
              display: inline-block;
              padding-left: 30px;

              &::after,
              &::before {
                width: 18px;
                height: 18px;
                left: 0;
                @extend .df-borRad4 !optional;
              }

              &::before {
                border: 2px solid #022170;
              }

              &::after {
                color: $client-primary-color;
              }
            }

            &:checked + label::after {
              background: transparent;
            }
          }
        }
      }
    }
  }

  &__dtls {
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;

    .df-plyrSel__thumb {
      @extend .df-bgSize--cover !optional;
    }

    .df-plyrSel__name {
      padding: 0 12px !important;
    }
  }
}

// overlays
.df-overlay {
  &__wrap {
    padding-top: 16px;
    z-index: -9999;
    background: rgba($client-primary-color, 0.8);
    position: fixed;
    left: 0;
    top: 0;
    min-width: 320px;
    max-width: 550px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    flex-direction: column;
    justify-content: flex-end;
    @extend %flexbox !optional;
    @include transition(padding-top 0.1s linear);

    &.df-active {
      padding-top: 44px;

      .df-toast__wrap {
        z-index: 999999;
        opacity: 1;
        @include transform(translateY(0%));
      }

      .df-overlay__container {
        min-height: calc(100% - 30px);
        @include transition(min-height 0.1s linear);
      }
    }

    &--contestToast {
      .df-toast__wrap {
        top: auto;
      }
    }
  }

  &__container {
    position: relative;
    z-index: 999999;
    min-height: 100%;
    background-color: $duck-egg-blue-two;
    flex-direction: column;
    @include border-radius(8px 8px 0 0);
    @extend %flexbox !optional;

    .df-cmo__tabs {
      z-index: 1;
    }

    &.df-overlay {
      &--alMat {
      }
      &--lineups {
        .df-overlay__title {
          flex-direction: column;
          span {
            &:not(:first-child) {
              margin-top: 2px;
              font-size: 12px;
              @extend .df-regularFF !optional;
              color: $neutral-medium;
            }
          }
        }
      }

      &--search {
        background: $white;

        .df-plyrSel__list {
          padding-bottom: 0;
        }

        .df-overlay__body {
          height: calc(100vh - 100px);
        }
      }

      &--filter {
        .df-overlay__body {
          background: $white;
          height: calc(100vh - 130px);
          position: relative;

          &::after {
            content: "";
            position: absolute;
            left: auto;
            right: 0;
            top: 1px;
            background: $duck-egg-blue;
            height: 100%;
            width: calc(100% - 120px);
          }
        }

        .df-overlay__cta {
          z-index: 1;
          cursor: pointer;
          position: absolute;
          left: auto;
          right: 16px;

          color: $client-secondary-color;
          font-size: 14px;
        }
      }

      &--favTeam {
        @extend .df-boldFF !optional;

        .df-overlay__header {
          border-bottom: 1px solid #e7e7e7;
        }

        .df-overlay__body {
          background: transparent;
          padding: 16px 0;
        }
      }

      &--transfer {
        .df-noDataFound {
          padding-top: 16px;
        }

        .df-alMat__list ul li {
          &:first-child {
            padding-top: 0;
          }

          .df-alMat__box {
            padding: 0;
          }
        }

        .df-alMat__type {
        }

        .df-alMat__box {
          padding: 0 16px 16px;
          @extend .df-boldFF !optional;
          justify-content: space-between;
        }

        .df-transfer__status {
          font-size: 14px;
          padding-left: 0;

          &.df-redTxt {
            margin-bottom: 0;
          }
        }

        .df-overlay__header {
          @extend .df-boldFF !optional;
          border-bottom: 1px solid #e7e7e7;
        }

        .df-overlay__body {
          background: transparent;
        }

        &-toast {
          position: absolute;
          width: 100%;
          min-height: auto;
          top: auto;
          bottom: 0;
          margin: 0;

          .df-overlay__header {
            border-bottom: 1px solid #e7e7e7;
          }

          .df-overlay__body {
            padding-top: 16px;
            padding-bottom: 70px;
            height: auto;
          }
        }
      }

      &--contest {
        // background-color: $client-primary-color;
        background: url(../images/contest-overlay-bg--supersmash.png?v=#{$image-version})
          no-repeat top left $cta-primary-color;
        @include background-size(contain);

        &-toast {
          position: absolute;
          width: 100%;
          min-height: auto !important;
          top: auto;
          bottom: 0;
          margin: 0;

          .df-overlay__body {
            padding-top: 16px;
            padding-bottom: 70px;
            height: auto;
          }

          .df-contest__title,
          .df-contest__thumb {
            cursor: default;
          }

          .df-contest__title span {
            color: $white;
            &:last-child {
              color: rgba($white, 0.8);
              margin-top: 6px;
            }
          }
        }

        .df-overlay__close,
        .df-overlay__title {
          color: $white;
        }

        .df-overlay__body {
          background: $duck-egg-blue;
          padding-bottom: 16px;
        }
        .df-contest__box-head {
          text-align: center;
        }
        .df-contest__thumb {
          display: none;
        }
      }

      &--player {
        justify-content: flex-start;
        background-color: $client-primary-color;

        // background: url(../images/contest-toast-bg.png?v=#{$image-version}) no-repeat top right $client-secondary-color;
        // @include background-size(400px);
        .df-player__thumb {
          position: relative;

          .df-plyrSel__status {
            left: -8px;
            bottom: 4px;
          }

          .df-plyrSel__batch {
            @extend .df-borRad2 !optional;
          }
        }
        .df-transfer__head--rowCol {
          margin-bottom: 0;
          border-bottom: 1px solid $light-blue-grey-two;
          img {
            vertical-align: bottom;
          }
        }
        .df-overlay__body {
          background: $duck-egg-blue;
        }

        .df-overlay__close,
        .df-overlay__title {
          color: $white;
        }

        .df-overlay__cta {
          position: absolute;
          left: 0;
          top: 50%;
          @include transform(translateY(-50%));
        }

        &-v2 {
          .df-accordion__body {
            margin-left: -16px;
            margin-right: -16px;
            padding-top: 0;
          }

          .df-player__tbl {
            padding: 0 16px;
          }

          .df-overlay__body {
            padding: 16px;
          }
        }
      }
      &--download,
      &--generic {
        min-height: auto;
        // background: $white;

        .df-overlay {
          &__header {
            border-bottom: 1px solid #e7e7e7;
          }

          &__body {
            padding: 24px 16px;
            padding-bottom: 70px;
            height: auto !important;
            padding-bottom: 120px;
          }

          &__foot ul {
            display: block;

            li {
              &:not(:last-child) {
                margin-bottom: 12px;
              }
            }
          }
        }

        .df-plyrSel__content {
          background-color: transparent;
          padding: 0 0 24px;

          &-txtTwo {
            color: $client-primary-color;
            line-height: 1.5;
          }
        }
      }

      &--booster {
        .df-overlay__body {
          padding: 24px 16px;
          padding-bottom: 70px;
        }

        .df-plyrSel__content {
          background-color: transparent;
          padding: 0 0 24px;
        }

        &-home {
          .df-booster__box {
            border-color: transparent !important;
          }
        }

        &-confirm {
          .df-overlay__body {
            padding-bottom: 120px;
          }

          .df-overlay__foot ul {
            display: block;

            li {
              &:not(:last-child) {
                margin-bottom: 12px;
              }
            }
          }
        }

        &-performance {
          .df-overlay__header {
            padding: 0px 16px;
            justify-content: center;
            align-items: center;
            @extend %flexbox !optional;

            .df-overlay__title {
              flex-basis: 100%;
            }
          }
        }

        &-confirm,
        &-performance {
          min-height: auto;

          .df-overlay__body {
            height: auto !important;
          }
        }

        &-confirm {
          .df-plyrSel__content-txtTwo {
            color: $client-primary-color;
          }
        }
      }

      &--badge {
        background-color: $white;

        &-nativeShare {
          min-height: auto;

          .df-overlay__body {
            height: auto;
          }

          .df-badge__box-lft {
            margin-right: auto;
            margin-left: auto;

            @media screen and (min-width: $media-mobile-max) {
              margin-right: 44px;
            }
          }
        }

        .df-overlay__header {
          min-height: auto;

          .df-badge {
            &__box {
              background-color: transparent;
              display: block;

              &-thumb {
                width: 110px !important;
                height: 120px !important;
                margin-bottom: 12px;
              }

              &-title {
                margin-bottom: 12px;
                font-size: 20px;
              }

              &-caption {
                font-size: 14px;
              }

              &-status {
                font-size: 12px;
              }

              &-title,
              &-caption {
                text-align: center;
              }

              &-progress {
                margin-top: 12px;
              }

              &-lft {
                margin-left: auto;
                margin-right: auto;
              }
            }
          }
        }

        .df-overlay__body {
          padding: 16px;
          padding-bottom: 70px;
        }

        .df-badge__box:not(:last-child) {
          margin-bottom: 16px;
        }

        &-sm,
        &-collector,
        &-share {
          min-height: auto;

          .df-overlay {
            &__header {
              padding-bottom: 0;
            }

            &__body {
              padding-top: 4px;
              height: auto;
            }

            &__foot ul {
              display: block;

              li {
                &:not(:last-child) {
                  margin-bottom: 12px;
                }
              }
            }
          }
        }

        &-sm {
          .df-overlay__header {
            padding-bottom: 16px;

            .df-badge__box-lft {
              margin-right: 0;
            }
          }
        }

        &-collector {
          min-height: auto;

          .df-overlay {
            &__header {
              padding-bottom: 0;
              padding-top: 0;

              .df-badge__box {
                padding-bottom: 0;
                padding-top: 0;

                &-caption {
                  @extend .df-mediumFF !optional;
                }

                &-thumb {
                  margin-top: -60px;
                }
              }

              .df-badge__box-lft {
                margin-right: 0;
              }
            }

            &__body {
              height: auto;
            }
          }
        }

        &-share {
          position: relative;

          &::after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            margin: 0 auto;
            top: -100%;
            height: 100%;
            width: calc(100% - 16px * 2);
            background-image: url(../images/stars-crackers.svg?v=#{$image-version});
            background-position: bottom center;
            background-repeat: no-repeat;
            @extend .df-bgSize--contain !optional;
            @include animation("df-popInUp .15s  .15s both");
          }

          .df-overlay__body {
            padding-bottom: 120px;
          }

          &--new {
            .df-badge__box-title {
              margin-bottom: 4px !important;
            }

            .df-badge__box-caption {
              text-align: center;
            }

            .df-overlay__header {
              .df-badge__box-caption {
                @extend .df-regularFF !optional;
              }
            }

            .df-overlay__body {
              .df-badge__box-caption {
                margin-top: 12px;
                margin-bottom: 16px;
                padding-bottom: 16px;
                position: relative;
                font-size: 16px;

                @media screen and (min-width: $media-mobile-max) {
                  font-size: 20px;
                  margin-bottom: 24px;
                  padding-bottom: 24px;
                }

                &::after {
                  content: "";
                  position: absolute;
                  left: 0;
                  right: 0;
                  bottom: 0;
                  margin: 0 auto;
                  height: 1px;
                  width: 20px;
                  background-color: $light-blue-grey-three;

                  @media screen and (min-width: $media-mobile-max) {
                    width: 24px;
                  }
                }
              }
            }
          }
        }
      }

      &--download {
        // min-height: auto;
        // background: $white;
        .df-overlay__body {
          padding-bottom: 24px;
        }
      }
    }

    &--badgTuts {
      // justify-content: center;

      .df-overlay__header {
        @media screen and (max-width: $media-mobile-max) {
          padding: 0;
          min-height: 20px;
        }
      }

      .df-overlay__body {
        height: auto !important;
        overflow: hidden;
      }

      .df-overlay__foot {
        padding-top: 30px;
        position: relative;

        .df-btn__white {
          max-height: 100%;
        }
      }
    }
  }

  &__title {
    position: relative;
    text-align: center;
    color: $client-primary-color;
    font-size: 16px;
    @extend .df-boldFF !optional;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;

    span {
      margin-left: 10px;
      margin-right: 10px;
    }
  }

  &__close {
    z-index: 1;
    font-size: 16px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 16px;
    left: 16px;
    justify-content: center;
    align-items: center;
    color: $client-primary-color;
    @extend %flexbox !optional;
  }

  &__header {
    position: relative;
    padding: 16px;
    font-size: 16px;
    min-height: 54px;
  }

  &__body {
    background: $white;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    height: calc(100vh - 150px);
  }

  &__foot {
    position: fixed;
    max-width: 550px;
    width: 100%;
    left: 0;
    bottom: 0;
    top: auto;
    padding: 16px;
    z-index: 9999999;

    ul {
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;

      li {
        text-align: center;
        padding: 0 6px;
        flex-basis: 100%;
        flex-grow: 1;

        .df-btn {
          @include box-shadow(0, 6px, 12px, -3px, rgba(16, 13, 72, 0.2));
        }
      }
    }

    &--hide {
      visibility: hidden;
      pointer-events: none;
    }
  }
}

// All Match Overlay List
.df-alMat {
  &__wrap {
  }

  &__list {
    overflow-y: auto;

    ul {
      li {
        padding: 16px;
        background: $white;

        &.df-nextMatch {
          background: $duck-egg-blue-two;
        }

        &:not(:last-child) {
          border-bottom: 1px solid rgba(#dcdcdc, 0.6);
        }
      }
    }
  }

  &__box {
    align-items: center;
    justify-content: flex-end;
    @extend %flexbox !optional;

    &.df-danger,
    &.df-success {
      align-items: flex-start;
    }

    &.df-success {
      color: $dark-aquamarine;
    }

    &.df-danger {
      color: $rust-red;
    }
  }

  &__lft {
    flex-basis: 60%;
  }

  &__rgt {
    flex-basis: 40%;
  }

  &__type,
  &__date {
    text-align: right;
    @extend .df-regularFF !optional;
  }

  &__type {
    margin-bottom: 4px;
    color: $client-primary-color;
    font-size: 12px;
  }

  &__date {
    font-size: 12px;
    color: $steel;
  }

  &__status {
    margin-top: 4px;
    text-align: right;
    font-size: 12px;
    @extend .df-boldFF !optional;
  }

  &__result {
    margin-top: 4px;
    font-size: 12px;
    @extend .df-regularFF !optional;
  }
}

// Fixtures
.df-fix {
  &__matchday {
    font-size: 10px;
    margin-bottom: 4px;
    display: block;
    color: $white;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__team {
    align-items: center;
    @extend %flexbox !optional;

    &-A,
    &-B {
      width: 64px;
      flex-shrink: 0;
      align-items: center;
      @extend %flexbox !optional;
    }

    &-vs {
      flex-basis: 36px;
      flex-shrink: 0;
      text-align: center;
      font-size: 14px;
      color: $steel;
      @extend .df-regularFF !optional;
    }

    &-name {
      margin: 0px 4px;
      font-size: 14px;

      color: $client-primary-color;
      @extend .df-boldFF !optional;
    }

    &-logo {
      flex-shrink: 0;
      width: 24px;
      height: 24px;
      filter: drop-shadow(0px 4px 8px rgba($black, 0.22));
      background-repeat: no-repeat;
      background-position: center center;
      @extend .df-bgSize--contain !optional;
    }
  }
}

// search bar
.df__searchBar {
  justify-content: space-between;
  align-items: center;
  @extend %flexbox !optional;

  &-lft {
    flex-basis: 100%;
  }

  &-rgt {
    padding-left: 16px;
  }

  &-cta {
    cursor: pointer;
    font-size: 14px;
    @extend .df-boldFF !optional;
    color: $cta-primary-color;
  }
}

// Filter
.df-filter {
  &__wrap {
    height: 100%;
    border-top: 1px solid #e7e7e7;
    justify-content: space-between;
    align-items: flex-start;
    @extend %flexbox !optional;
  }

  &__lft {
    width: 120px;
    background: $white;
    flex-shrink: 0;
    height: 100%;
  }

  &__rgt {
    padding: 0 12px;
    flex-basis: 100%;
    background: $duck-egg-blue;
    position: relative;
    z-index: 1;

    ul {
      padding-bottom: 70px;

      li {
        padding: 16px 0;
        border-bottom: 1px solid #e7e7e7;
      }
    }
  }

  &__title {
    cursor: pointer;
    padding: 16px;
    text-align: left;
    font-size: 14px;
    color: $steel;
    @extend .df-boldFF !optional;

    &.df-active {
      span {
        position: relative;
        display: inline-block;
        padding-right: 8px;

        &::after {
          content: "";
          width: 4px;
          height: 4px;
          background: $green;
          position: absolute;
          left: auto;
          top: 0;
          right: 0;
          @extend .df-borRad100 !optional;
        }
      }
    }

    &.df-selected {
      color: $client-secondary-color;
      background: $duck-egg-blue;
    }
  }

  &__subTxt {
    display: block;
    font-size: 12px;

    span {
      &:not(:last-child) {
        padding-right: 8px;
      }
    }
  }
}

// toast msg
.df-toast {
  &__wrap {
    position: fixed;
    top: 0;
    z-index: -999999;
    min-width: 320px;
    max-width: 550px;
    width: 100%;
    padding: 16px 8px 24px;
    color: $white;
    text-align: center;
    font-size: 12px;
    opacity: 0;
    @include transform(translateY(100%));
    @include transition(transform 0.1s linear, opacity 0.1s linear);

    &--topAppear {
      @include transform(translateY(-100%));
    }

    &.df-success {
      background: $green;
    }

    &.df-danger {
      background: $red;
    }

    &.df-show {
      z-index: 999999;
      opacity: 1;
      @include transform(translateY(0%));
    }
  }

  &__title {
    @extend .df-boldFF !optional;
  }

  &__text {
    @extend .df-regularFF !optional;
  }
}

// Fav team
.df-favTeam {
  &__wrap {
  }

  &__txt {
    text-align: center;
    font-size: 12px;
    color: $client-primary-color;
    @extend .df-regularFF !optional;
  }

  &__list {
    padding: 20px 0;

    ul {
      li {
        cursor: pointer;
        margin-bottom: 12px;

        &.df-active {
          .df-favTeam__cta {
            border-color: $client-secondary-color;

            &::before {
              background: $client-secondary-color;
            }
          }
        }
      }
    }
  }

  &__box {
    padding: 16px;
    background: $white;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-boxShadow--sm !optional;

    &-lft {
      flex-basis: 100%;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;
    }

    &-rgt {
      flex-basis: 50px;
    }
  }

  &__thumb {
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    background-position: center center;
    background-repeat: no-repeat;
    @extend .df-bgSize--contain !optional;
  }

  &__team {
    padding-left: 16px;
    flex-basis: 100%;

    &-name {
      font-size: 16px;
      color: $client-primary-color;
      @extend .df-boldFF !optional;
      margin-bottom: 4px;
    }

    &-stats {
      font-size: 14px;
      color: $steel;
      @extend .df-regularFF !optional;
    }
  }

  &__cta {
    position: relative;
    width: 24px;
    height: 24px;
    background: $white;
    border: 1px solid $steel;
    justify-content: center;
    align-items: center;
    @extend .df-borRad100 !optional;
    @extend %flexbox !optional;

    &::before {
      content: "";
      position: absolute;
      width: calc(100% - 10px);
      height: calc(100% - 10px);
      @extend .df-borRad100 !optional;
    }
  }
}

// Transfer
.df-transfer {
  &__wrap {
  }

  &__info {
    color: $white;
    margin: 0 -16px;
    align-items: center;
    justify-content: space-between;
    @extend %flexbox !optional;

    &.df-success {
      background: $green;

      .df-transfer__info-lft {
        background: $dark-aquamarine;
      }
    }

    &.df-danger {
      background: $red;

      .df-transfer__info-lft {
        background: $dark-red;
      }
    }

    &.df-active {
      .df-transfer__info-lft {
        margin-left: 0;
      }

      .df-transfer__info-rgt {
        font-size: 0;

        i {
          font-size: 16px;
          margin-right: 0;
        }
      }
    }

    &-lft,
    &-mid,
    &-rgt {
      font-size: 14px;
      padding: 12px 16px;
      @extend .df-regularFF !optional;
      line-height: 1.5;
      @include transition(font-size 0.15s linear);

      @media screen and (min-width: $media-mobile-max) {
        font-size: 16px;
      }

      em {
        margin-right: 4px;
        @extend .df-boldFF !optional;
      }
    }

    &-lft,
    &-rgt {
      span {
        position: relative;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;

        i {
          width: 20px;
          height: 20px;
          justify-content: center;
          align-items: center;
          @extend %flexbox !optional;
        }
      }
    }

    &-lft {
      margin-left: -140px;
      @include border-radius(0px 100px 100px 0px);
      @include transition(margin-left 0.3s linear);

      @media screen and (min-width: $media-mobile-max) {
        margin-left: -155px;
      }

      span {
        i {
          margin-left: 12px;
          cursor: pointer;
          border: 1px solid $white;
          @extend .df-borRad100 !optional;

          &::after {
            content: "";
            width: calc(100% - 8px);
            height: 2px;
            background: $white;
          }
        }
      }
    }

    &-mid {
      flex-grow: 1;
    }

    &-rgt {
      cursor: pointer;
      white-space: nowrap;
      text-align: right;

      span {
        position: relative;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;

        i {
          margin-right: 12px;

          &::after {
            content: "\e90c";
            font-family: "dfi" !important;
            font-style: normal;
          }
        }
      }
    }
  }

  &__head {
    font-size: 14px;
    background: $white;
    padding: 10px 16px;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-boxShadow--sm !optional;

    span {
      @extend .df-regularFF !optional;
      color: rgba($client-primary-color, 0.6);

      em {
        color: $client-primary-color;
        @extend .df-boldFF !optional;
      }
    }

    &--rowCol {
      margin-bottom: 4px;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;

      span {
        text-align: center;
        font-size: 10px;
        flex-basis: calc(100% / 4);
        flex-grow: 1;
        flex-shrink: 0;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 12px;
        }

        em {
          font-size: 16px;
          display: block;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 18px;
          }
        }

        &:not(:last-child) {
          border-right: 1px solid $light-blue-grey-two;
        }
      }

      .df-contest__viewMore {
        display: inline-block;
      }
    }
  }

  &__status {
    margin-bottom: 12px;
    font-size: 14px;
    color: $client-primary-color;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-regularFF !optional;

    em {
      @extend .df-boldFF !optional;
    }

    &--nmp {
      margin-bottom: 0;
      padding-left: 16px;
    }
  }

  &__fix {
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;

    ul {
      @extend %flexbox !optional;

      li {
        cursor: pointer;

        font-size: 14px;
        color: $light-grey-blue;
        letter-spacing: 0.18px;
        @extend .df-boldFF !optional;

        em {
          @extend .df-regularFF !optional;
        }

        &:not(:last-child) {
          padding-right: 32px;
        }
      }
    }

    .df-pitch__booster {
      @extend .df-pitch__booster--overwrite;
    }
  }

  &__tbl {
    .df-tbl {
      &__head {
        position: relative;

        &::after {
          content: "\e90c";
          font-family: "dfi" !important;
          margin-left: calc(100% - 4%);
          margin-top: -28px;
          color: $steel;
          float: left;
          clear: both;
        }

        .df-tbl__cell {
          text-align: center;
          border-bottom: 1px solid rgba(#dcdcdc, 0.6);
          font-size: 12px;
          color: $steel;
          padding-bottom: 12px;
          @extend .df-regularFF !optional;
        }
      }

      &__body {
        .df-tbl__cell {
          width: 50%;
          padding: 8px 0 0;

          &--lft {
            border-right: 1px solid #e4e4e4;
          }

          &--mid {
            text-align: center;
            width: 100px;
          }
        }
      }

      &__cell {
        &--lft {
          i {
            padding: 0 8px;
          }
          .df-transfer__plyr-scr {
            text-align: right;
          }
          .df-transfer__plyr {
            margin: 0 16px 0 0;

            &-name {
              color: $dark-aquamarine;
            }
          }
        }

        &--rgt {
          text-align: right;

          i {
            padding: 0 8px;
            color: $dark-aquamarine;
          }
          .df-transfer__plyr-scr {
            text-align: left;
          }
          .df-transfer__plyr {
            margin: 0 0 0 16px;
            flex-direction: row-reverse;
          }
        }
      }
    }
  }

  &__plyr {
    margin-top: 8px;
    margin-bottom: 8px;
    border-bottom: 1px solid #e4e4e4;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;

    &-thumb {
      flex-shrink: 0;
      height: 48px;
      width: 48px;
      background-repeat: no-repeat;
      background-position: center bottom;
      @extend .df-bgSize--contain !optional;
    }

    &-dtls {
      flex-basis: 100%;
      padding: 0 4px;
    }

    &-scr {
      font-size: 12px;
      color: $client-primary-color;
      @extend .df-mediumFF !optional;
      em {
        font-size: 10px;
        color: $neutral-medium;
        @extend .df-regularFF !optional;
      }
    }

    &-name {
      color: rgba($client-primary-color, 0.6);
      font-size: 12px;
      @extend .df-regularFF !optional;
    }

    &-skill {
      font-size: 10px;
      @extend .df-regularFF !optional;
      color: $client-primary-color;
    }
  }

  &__icon {
    position: relative;
    color: $client-primary-color;
    width: 80px;
    height: 80px;
    margin: 0 auto 16px;
    // border: 1px solid $client-primary-color;
    background: url(../images/tick-animi.gif?v=#{$image-version}) center center
      no-repeat;
    @include background-size(100px);
    @extend .df-borRad100 !optional;

    // &::after {
    //   content: "";
    //   // content: "\e90b";
    //   // font-family: "dfi" !important;
    //   height: 100%;
    //   width: 100%;
    //   // font-size: 36px;
    //   // justify-content: center;
    //   // align-items: center;
    //   // @extend %flexbox !optional;

    // }
  }

  &__content {
    text-align: center;
    padding-bottom: 20px;
    margin-top: 8px;
    font-size: 14px;
    color: $body-text;
    @extend .df-regularFF !optional;

    span {
      display: block;
    }
  }

  &__header {
    padding: 16px 16px 4px;
    text-align: center;
    color: $client-primary-color;
    font-size: 14px;
    @extend .df-boldFF !optional;

    &--fbox {
      padding-top: 10px;
      padding-bottom: 0;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;

      .df-pitch__booster {
        @extend .df-pitch__booster--overwrite;
      }
    }
  }
}

// Player Info Card
.df-player {
  &__head {
    background: url(../images/player-popup-bg.png?v=#{$image-version}) top left
      no-repeat $client-primary-color;
    min-height: 92px;
    padding: 12px 16px 0;
    justify-content: flex-start;
    align-items: center;
    @include background-size(contain);
    @extend %flexbox !optional;
  }

  &__body {
    padding-bottom: 70px;
  }

  &__thumb {
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    background-repeat: no-repeat;
    background-position: top center;
    @extend .df-bgSize--contain !optional;
  }

  &__stats {
    flex-basis: 100%;

    ul {
      padding-left: 40px;
      justify-content: flex-start;
      align-items: flex-start;
      @extend %flexbox !optional;

      li {
        flex-grow: 1;
        flex-basis: 100%;
      }
    }

    &-txt {
      font-size: 12px;
      color: rgba($white, 0.6);
      @extend .df-regularFF !optional;
    }

    &-num {
      font-size: 28px;
      color: $white;
      @extend .df-boldFF !optional;
      line-height: 1;
    }
  }

  &__profile {
    background: $white;
    padding: 16px;
    border-bottom: 1px solid rgba(#dcdcdc, 0.6);

    &-row {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      &:first-child {
        justify-content: space-between;
      }

      &:not(:last-child) {
        margin-bottom: 6px;
      }

      &.df-injured,
      &.df-unavailable,
      &.df-banned,
      &.df-in,
      &.df-out {
        .df-player__profile-icon {
          position: relative;
          justify-content: center;
          align-items: center;
          @extend %flexbox !optional;

          &::after {
            content: "";
            width: 6px;
            height: 6px;
            @extend .df-borRad100 !optional;
          }
        }

        .df-player__profile-txt {
          span {
            font-size: 12px;

            @media screen and (min-width: $media-mobile-max) {
              font-size: 14px;
            }

            em {
              font-size: 8px;

              @media screen and (min-width: $media-mobile-max) {
                font-size: 12px;
              }
            }
          }

          a {
            color: $client-primary-color;
          }
        }
      }

      &.df-in {
        .df-player__profile-icon {
          &::after {
            background: $dark-aquamarine;
          }
        }

        .df-player__profile-txt {
          span {
            &:first-child {
              margin-right: 4px;
              color: $dark-aquamarine;
            }
          }
        }
      }

      &.df-out {
        .df-player__profile-txt {
          span {
            @extend %flexbox !optional;
          }
        }

        .df-player__profile-icon {
          &::after {
            background: $tomato-red;
          }
        }
      }

      &.df-banned,
      &.df-injured,
      &.df-unavailable {
        .df-player__profile-icon {
          &::after {
            width: 12px;
            height: 12px;
            background-position: center center;
            background-repeat: no-repeat;
            @extend .df-bgSize--contain !optional;
            @include border-radius(0);
          }
        }

        .df-player__profile-txt {
          span {
            &:first-child {
              margin-right: 4px;
              color: $tomato-red;
            }
          }
        }
      }

      &.df-banned {
        .df-player__profile-icon {
          &::after {
            background-image: url(../images/ic-warning.svg?v=#{$image-version});
          }
        }
      }

      &.df-injured {
        .df-player__profile-icon {
          &::after {
            background-image: url(../images/injured.svg?v=#{$image-version});
          }
        }
      }

      &.df-unavailable {
        .df-player__profile-icon {
          &::after {
            background-image: url(../images/unavailable.svg?v=#{$image-version});
          }
        }
      }
    }

    &-name {
      font-size: 20px;
      @extend .df-boldFF !optional;
      color: $client-primary-color;

      &.df-unavailable,
      &.df-injured {
        position: relative;
        display: inline-grid;
        padding-right: 16px;
        margin-right: 16px;

        &::after {
          content: "";
          width: 12px;
          height: 12px;
          position: absolute;
          left: auto;
          right: 0;
          top: 50%;
          @include transform(translateY(-50%));
        }

        span {
          @include text-truncate;
        }
      }

      // &.df-unavailable {
      //   &::after {
      //     background: url(../images/booster-icon.svg?v=#{$image-version}) center center no-repeat;
      //     @include background-size(contain);
      //   }
      // }

      // &.df-injured {
      //   &::after {
      //     background: url(../images/injured.svg?v=#{$image-version}) center center no-repeat;
      //     @include background-size(contain);
      //   }
      // }
    }

    &-cta {
      display: none;
      cursor: pointer;
      text-align: right;
      font-size: 12px;
      flex-shrink: 0;
      padding-left: 16px;
      @extend .df-regularFF !optional;
      color: $client-secondary-color;

      i {
        margin-left: 4px;
        font-size: 10px;
      }
    }

    &-icon,
    &-logo {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }

    &-logo {
      background-position: center center;
      background-repeat: no-repeat;
      @extend .df-bgSize--contain !optional;
    }

    &-skill {
      font-size: 12px;
      @extend .df-regularFF !optional;

      span {
        color: $steel;

        &:not(:last-child) {
          color: $client-primary-color;
        }
      }
    }

    &-icon {
      color: $steel;
      font-size: 12px;
      text-align: center;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
    }

    &-txt {
      font-size: 14px;
      color: $steel;
      @extend .df-regularFF !optional;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;
    }
  }

  &__tbl {
    margin: 12px 0;

    .df-tbl {
      width: 100%;
      table-layout: fixed;

      &__head {
        .df-tbl__cell {
          font-size: 12px;
          color: rgba($black, 0.6);
          @extend .df-regularFF !optional;
        }
      }

      &__body {
        .df-tbl__cell {
          font-size: 14px;
          color: $client-primary-color;
          @extend .df-regularFF !optional;
        }

        .df-tbl__row {
          .df-tbl__cell {
            border-top: 1px solid #e7e7e7;
          }
        }
      }

      &__cell {
        padding: 8px 0px;

        &--evt {
        }

        &--act,
        &--pts {
          text-align: right;
        }
      }
    }
  }

  &--complete {
    .df-accordion {
      margin: 0;

      &__box {
        padding: 0;
      }

      &__head {
        padding-bottom: 0;

        &::after {
          bottom: 4px;
          right: 16px;
        }
      }

      &__body {
        border-top: none;
        padding: 0 16px;

        .df-player__tbl {
          margin-top: 0;
        }
      }
    }

    .df-alMat {
      &__box {
        display: block;
      }

      &__lft {
        width: 100%;
        flex-basis: 100%;
        margin-bottom: 8px;
      }

      &__rgt {
        position: absolute;
        bottom: 9px;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;
      }

      &__type {
        margin-bottom: 0;
        margin-right: 8px;
      }
    }

    .df-player {
      &__fBox {
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;

        &--head {
          background: $white;
          font-size: 12px;
          padding: 8px 16px;
          border-bottom: 1px solid #e7e7e7;
          color: $steel;
          @extend .df-regularFF !optional;
        }

        &--body {
          padding: 8px 16px;
          align-items: flex-start;
          position: relative;
          min-height: 58px;

          .df-player__fItem--cent,
          .df-player__fItem--pts {
            font-size: 14px;
            color: $client-primary-color;
            @extend .df-boldFF !optional;
          }
        }
      }

      &__fItem {
        &--mat {
          flex-basis: 100%;
        }

        &--cent,
        &--pts {
          flex-basis: 128px;
        }

        &--cent,
        &--pts {
          text-align: right;
        }
      }
    }

    .df-fix__team {
      &-A,
      &-B {
        width: 30px;
      }

      &-name {
        margin: 0;
      }

      &-vs {
        flex-basis: 28px;
      }

      &-logo {
        display: none;
      }
    }
  }
}

// Pitch View
.df-pitch {
  &__wrap {
    height: 100%;
    min-height: 100%;
    background-image: url(../images/pitch-pattern.jpg?v=#{$image-version});
    background-repeat: repeat-y;
    background-position: center top;
    flex-direction: column;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    @extend .df-bgSize--contain !optional;
    @extend %flexbox !optional;
    @include border-radius(8px 8px 0px 0px);

    @media screen and (min-width: $media-mobile-max) {
      overflow: hidden;
      @include border-radius(0px);
    }

    .df-overlay {
      &__header {
        background: $client-primary-color;
      }

      &__close,
      &__title {
        color: $white;
      }
    }
  }

  &__area {
    position: relative;
    min-height: 100%;
    // height: 100%;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;
    // background: url(../images/pitch__thumbnail.png?v=#{$image-version}) center center no-repeat;

    @media screen and (min-width: $media-mobile-max) {
      min-height: auto;
      height: 100%;
      overflow: auto;
    }
  }

  &__box {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 12px 0 0;
    background-image: url(../images/pitch.png?v=#{$image-version});
    background-repeat: no-repeat;
    background-position: center center;
    @extend %flexbox !optional;
    @include background-size(calc(100% - 20%));
  }

  &__row {
    width: 100%;

    &.df-view {
      &--1 {
      }

      &--2 {
      }

      &--3 {
      }

      &--4 {
      }

      &--5,
      &--6 {
        .df-pitch__card {
          flex-wrap: wrap;
        }
      }

      &--5 {
        .df-pitch__card {
          .df-pitch__plyr {
            flex-basis: 33.33%;
            width: 33.33%;
          }
        }
      }

      &--6 {
        .df-pitch__card {
          .df-pitch__plyr {
            flex-basis: 25%;
            width: 25%;
          }
        }
      }
    }
  }

  &__title {
    text-align: center;
    font-size: 10px;
    color: rgba($white, 0.8);
    margin-bottom: 8px;
    @extend .df-regularFF !optional;
  }

  &__card {
    padding: 8px 0;
    justify-content: space-evenly;
    align-items: center;
    @extend %flexbox !optional;
  }

  &__plyr {
    will-change: transform;
    margin: 0 auto;
    position: relative;
    text-align: center;
    @include animation("df-zoomIn .1s linear");

    &.df-triBoos,
    &.df-cap,
    &.df-vcap {
      .df-pitch__plyr-thumb {
        position: relative;

        &::after {
          content: "";
          background: $client-primary-color;
          position: absolute;
          left: auto;
          right: -16px;
          top: 0;
          width: 16px;
          height: 16px;
          color: $white;
          font-size: calc(100% - 8px);
          justify-content: center;
          align-items: center;
          @extend %flexbox !optional;
          @extend .df-boldFF !optional;
          @extend .df-borRad100 !optional;

          @media screen and (min-width: $media-mobile-max) {
            width: 20px;
            height: 20px;
            font-size: calc(100% - 4px);
          }
        }
      }
    }

    &.df-triBoos {
      .df-pitch__plyr-thumb {
        &::after {
          content: "\e91e";
          font-family: dfi !important;

          @media screen and (min-width: $media-mobile-max) {
            font-size: calc(100% - 2px);
          }
        }
      }
    }

    &.df-cap {
      .df-pitch__plyr-thumb {
        &::after {
          content: "C";
        }
      }
    }

    &.df-vcap {
      .df-pitch__plyr-thumb {
        &::after {
          content: "VC";
        }
      }
    }

    &.df-uncap {
      .df-pitch__flight {
        visibility: visible;
        width: 16px;
        height: 16px;
        background-image: url(../images/uncapped.svg?v=#{$image-version});
        background-repeat: no-repeat;
        background-position: center center;
        @extend .df-bgSize--contain !optional;

        .dfi-flight {
          display: none;
        }
      }
    }

    &.df-flight {
      .df-pitch__flight {
        visibility: visible;
      }
    }

    &.df-unavailable,
    &.df-injured,
    &.df-banned {
      .df-pitch__plyr-injured {
        position: absolute;
        left: auto;
        right: 8px;
        background-position: center center;
        background-repeat: no-repeat;
        @extend .df-bgSize--contain !optional;

        @media screen and (max-width: $media-mobile-min) {
          right: -4px;
        }
      }
    }

    &.df-injured {
      .df-pitch__plyr-injured {
        top: 26px;
        width: 12px;
        height: 12px;
        background-image: url(../images/injured.svg?v=#{$image-version});

        // @media screen and (min-width: $media-mobile-max) {
        //   top: 36px;
        // }
      }
    }

    &.df-banned {
      .df-pitch__plyr-thumb,
      .df-pitch__plyr-name,
      .df-pitch__plyr-num {
        opacity: 0.5;
      }

      .df-pitch__plyr-name span {
        background: $rust-red;
      }

      .df-pitch__plyr-injured {
        top: 22px;
        opacity: 1;
        width: 18px;
        height: 16px;
        background-image: url(../images/ic-warning.svg?v=#{$image-version});
      }
    }

    &.df-unavailable {
      .df-pitch__plyr-injured {
        top: 22px;
        opacity: 1;
        width: 16px;
        height: 16px;
        background-image: url(../images/unavailable.svg?v=#{$image-version});
      }
    }

    &.df-in,
    &.df-out {
      .df-pitch__plyr-name {
        &::after {
          content: "";
          position: absolute;
          left: -3px;
          top: 50%;
          width: 6px;
          height: 6px;
          @extend .df-borRad100 !optional;
          @include transform(translateY(-50%));
        }
      }
    }

    &.df-in {
      .df-pitch__plyr-name {
        &::after {
          background: $green;
        }
      }
    }

    &.df-out {
      .df-pitch__plyr-name {
        &::after {
          background: $red;
        }
      }
    }

    &-box {
      position: relative;
      display: inline-block;
      width: 90px;
      cursor: pointer;

      @media screen and (max-width: $media-mobile-min) {
        width: 80px;
      }
    }

    &-icon {
      position: absolute;
      left: -16px;
      top: auto;
      bottom: 0;
      height: 100%;
      font-size: 10px;
      text-align: center;
      color: $light-grey-blue;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;
      @extend %flexbox !optional;

      @media screen and (max-width: $media-mobile-min) {
        left: -14px;
      }

      @media screen and (min-width: $media-mobile-max) {
        font-size: 12px;
      }
    }

    &-thumb {
      position: relative;
      margin: 0 auto;
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center top;
      @extend .df-bgSize--contain !optional;

      @media screen and (min-width: $media-mobile-max) {
        width: 48px;
        height: 48px;
      }
    }

    &-name {
      text-align: center;
      margin: 0 auto;
      position: relative;
      width: 72px;
      font-size: 12px;
      color: $white;
      @extend .df-regularFF !optional;

      @media screen and (max-width: $media-mobile-min) {
        width: calc(100% - 10px);
      }

      span {
        background: $client-primary-color;
        display: block;
        padding: 2px 6px;
        @extend .df-borRad100 !optional;
        @include text-truncate;
      }
    }

    &-num {
      font-size: 12px;
      color: $white;
      @extend .df-boldFF !optional;

      em {
        font-size: 10px;
        @extend .df-regularFF !optional;
      }
    }
  }

  &__flight {
    display: none;
    visibility: hidden;
  }

  &__team {
    margin-bottom: 2px;
    padding: 0 2px;
    background-color: rgba(#31353e, 0.8);
    font-size: 10px;
    color: $white;
    @extend .df-regularFF !optional;
    @include border-radius(2px);
  }

  &__fix {
    position: relative;
    padding: 12px 16px;
    background: rgba(#31353e, 0.4);
    font-size: 14px;
    color: $white;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    flex-shrink: 0;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }

    .df-fix__team {
      // &-logo {
      //   display: none;
      // }

      &-name {
        padding: 2px 4px;
        color: $white;
        @extend .df-boldFF !optional;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }
      }
      &-picked {
        padding: 0 4px;
      }
      &-vs {
        @media screen and (min-width: $media-mobile-max) {
          font-size: 10px;
        }
      }

      .df-teamA {
        .df-fix__team-name {
          color: $default;
          background-color: $white;
          @extend .df-borRad2 !optional;
        }
      }

      .df-teamB {
        .df-fix__team-name {
          background-color: $default;
          color: $white;
          @extend .df-borRad2 !optional;
        }
      }

      &-A,
      &-B {
        width: auto;
      }

      &-vs {
        flex-basis: auto;
        padding: 0 4px;
        color: $white;
      }

      &-B {
        flex-direction: row-reverse;
      }
    }

    .df-pitch__booster {
      position: relative;
      left: 0;
      top: 0;
      @include transform(translateX(0%) translateY(0%));

      .df-cmo__pickPlayer-booster-icon {
        width: 24px;
        height: 24px;

        @media screen and (min-width: $media-mobile-max) {
          width: 32px;
          height: 32px;
        }
      }
    }

    &-lft {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;
      @extend .df-boldFF !optional;

      em {
        margin: 0 4px;
        @extend .df-regularFF !optional;
      }

      .df-cmo__fix {
        padding: 0;

        &-lft {
          ul {
            li {
              color: $white;
            }
          }
        }
      }
    }

    &-rgt {
      // position: absolute;
      // left: 50%;
      // top: 50%;
      // @include transform(translate(-50%, -50%));
      @extend .df-regularFF !optional;

      em {
        @extend .df-boldFF !optional;
      }
    }

    .df-cmo__pickPlayer {
      &-mid {
        margin-left: 0;
        justify-content: center;
        position: absolute;
        left: 0;
        right: 0;
        .df-cmo__fix {
          padding: 0;
        }
      }
      &-top {
        width: 100%;
        padding: 0;
      }
      &-txt {
        font-size: 8px;
        color: $white;
      }
      &-num {
        font-size: 14px;
        em {
          font-size: 10px;
        }
      }
    }
  }

  &__freshView {
    text-align: center;
    padding: 24px;
    background: rgba($black, 0.3);
    color: $white;
    @extend .df-boldFF !optional;
    font-size: 14px;
    width: calc(100% - 4px * 30);
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }

    &-cta {
      margin-top: 16px;
      display: block;

      @media screen and (min-width: $media-mobile-max) {
        display: none;
      }
    }
  }

  &__booster {
    // padding-left: 8px;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 2;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;
    @include transform(translateX(-50%) translateY(-50%));
    // display: none;

    &--reversed {
      background: #ffebeb;
      padding: 4px;
      @extend .df-borRad100 !optional;
    }

    .df-cmo__pickPlayer-booster-icon {
      background-color: $white;
    }
  }
}

// Contest Page
.df-contest {
  &--won {
    .df-plyrSel__list {
      @include linearGradient(159deg, $yellow-tan 28%, $marigold 73%);

      ul {
        li {
          &.df-remove {
            background-color: transparent !important;
          }

          .df-tbl__cell--pts,
          .df-tbl__cell--amt em {
            color: $client-primary-color !important;
          }
        }
      }
    }
  }

  &--create {
    .df-contest__title {
      border-bottom: 1px solid #e7e7e7;
      padding-bottom: 12px;
      text-align: center;
      span:last-child {
        font-size: 14px;
        @extend .df-boldFF !optional;
      }
    }
  }

  &__wrap {
    padding-top: 12px;
    background: $duck-egg-blue;
    position: relative;
    padding: 16px;

    &--input {
      min-height: auto;
      padding-top: 16px;

      .df-contest__box {
        padding: 16px;
      }

      .df-form {
        &__control {
          margin-bottom: 16px;

          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }

      .df-input {
        input {
          background: transparent;
          border-bottom: 1px solid #d5d5d5;
          @include border-radius(0);
          @extend .df-regularFF !optional;
        }
      }

      // .df-cmo__footer {
      //   padding: 0;

      //   ul {
      //     li {
      //       padding: 0;

      //       .df-btn {
      //         @include border-radius(0);
      //       }
      //     }
      //   }
      // }
    }

    .df-transfer {
      &__wrap {
        margin-top: 16px;
        background: $white;
        @extend .df-boxShadow--sm !optional;
      }

      &__info {
        margin: 0;
        color: $cobalt-two;

        &-mid {
        }

        &-rgt {
          span i {
            @extend .df-boldFF !optional;
            margin-right: 0;
          }
        }
      }
    }

    .df-noDataFound {
      padding-top: 16px;
    }
  }

  &__mHead {
    background: $white;
    margin: 0 -16px;

    &.df-contest--team {
      .df-contest__thumb {
        @include border-radius(0);
        @extend .df-bgSize--contain !optional;
      }
    }

    .df-contest {
      &__box {
        &-head {
          min-height: 76px;
        }
      }

      &__title {
        span {
          &:first-child {
            color: $client-secondary-color;
          }
        }
      }
    }
  }

  &__box {
    overflow: hidden;
    position: relative;
    min-height: 4 * 26px;
    background: $white;
    box-shadow: 0px 3px 6px -1px rgba(26, 26, 26, 0.2),
      0px 1px 8px rgba(26, 26, 26, 0.08);
    @extend .df-boxShadow--sm !optional;
    @extend .df-borRad8 !optional;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &.df-abandone {
      .df-contest__dateLine {
        justify-content: flex-start;

        span:last-child {
          text-align: left;
        }
      }
    }

    &--team {
      .df-contest__thumb {
        @include border-radius(0);
        @extend .df-bgSize--contain !optional;
      }
    }

    &-top {
      border-bottom: 1px solid #e7e7e7;
      @include linearGradient(
        top,
        rgba($client-primary-color, 0.1) 0%,
        rgba($client-primary-color, 0) 100%
      );

      .df-plyrSel__list {
        padding-bottom: 0 !important;
      }
    }

    &.df-teamPreview {
      .df-transfer__status {
        padding-left: 16px;
        padding-right: 16px;

        span {
          display: block;
          width: 100%;
        }
      }

      .df-cmo__fix {
        padding: 0 16px 12px;
        position: relative;
        align-items: flex-end;

        &-lft ul li {
          color: $steel;
        }

        &-rgt {
          color: $client-primary-color;
          cursor: default;
          @extend .df-boldFF !optional;

          em {
            margin-right: 4px;
          }

          .df-pitch__booster {
            @extend .df-pitch__booster--overwrite;
          }
        }
      }

      .df-contest {
        &__box {
          &-head {
            color: $client-primary-color;
            justify-content: space-between;

            span {
              font-size: 14px;
              justify-content: flex-start;
              align-items: center;
              @extend %flexbox !optional;
              @extend .df-boldFF !optional;

              &.icon-share {
                cursor: pointer;
                margin-left: 24px;
                width: 20px;
                height: 20px;
                flex-basis: 20px;
                flex-shrink: 0;
                font-size: 18px;
                text-align: center;
                align-items: center;
                justify-content: center;
                @extend %flexbox !optional;
              }

              &.df-transfer__overall {
                color: $client-primary-color;
                @extend .df-regularFF !optional;

                em {
                  margin-right: 4px;
                  @extend .df-boldFF !optional;
                }
              }
            }
          }

          &-body {
            padding-bottom: 16px;
          }
        }

        &__cta {
          padding-top: 0;
          padding-bottom: 0;
        }
      }
    }

    &.df-freezed {
      .df-cmo__fix {
        &::after {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          top: auto;
          bottom: 0;
          height: 1px;
          margin: 0 auto;
          width: calc(100% - 16px * 2);
          background: #e7e7e7;
        }
      }
    }

    &-thumb {
      padding: 4px 16px 12px;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;

      &-txt {
        flex-basis: 100%;

        span {
          display: block;

          &:first-child {
            @extend .df-regularFF !optional;
            font-size: 12px;
            color: $steel;
            margin-bottom: 4px;
          }

          &:last-child {
            font-size: 14px;
            @extend .df-boldFF !optional;
            color: $text-dark;
          }
        }
      }

      &-img {
        flex-basis: 44px;
        flex-shrink: 0;

        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }

    &-head {
      position: relative;
      padding: 12px 16px;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      .df-pitch__booster {
        position: absolute;
        left: 50%;
        top: 50%;
        @include transform(translateX(-50%) translateY(-50%));
      }
    }

    &-foot {
      cursor: pointer;
      padding: 12px 16px;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;

      span {
        font-size: 14px;
        @extend .df-regularFF !optional;
        color: $client-primary-color;
      }
    }
  }

  &__cta {
    padding: 16px;

    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
  }

  &__pitch {
    cursor: pointer;
    // margin: 0 16px;
    overflow: hidden;
    min-height: 95px;
    height: auto;
    _height: 95px;
    background: url(../images/my-team-pitch.png?v=#{$image-version}) no-repeat
      center center;
    @include background-size(cover);
    @extend .df-borRad8 !optional;
    &-edit {
      cursor: pointer;
      i {
        margin-left: 4px;
      }
    }
    &-view {
      padding: 16px 0 0;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;

      @media screen and (max-width: $media-mobile-sm-min) {
        display: block;
        // flex-direction: column;
        padding-top: 0;
      }

      &-lft,
      &-rgt {
        flex-basis: 50%;
        // padding: 8px 16px;
        padding: 0 20px;

        @media screen and (min-width: $media-mobile-max) {
          padding: 0 32px;
        }

        @media screen and (max-width: $media-mobile-sm-min) {
          width: 100%;
        }
      }

      &-lft {
        flex-basis: auto;
        padding-right: 0;
      }

      &-rgt {
        .df-pitch__plyr {
          &:not(:last-child) {
            @media screen and (min-width: $media-mobile-max) {
              margin-right: 48px;
            }
          }

          &-box {
            @media screen and (min-width: $media-mobile-max) {
              width: 100%;

              .df-pitch__plyr-injured {
                top: 34px;
                right: -2px;
              }
            }
          }
        }

        // flex-basis: calc(100% - 0px);
        @media screen and (max-width: $media-mobile-sm-min) {
          padding: 0;
          margin: 8px 0;
        }
      }

      &-lft {
        @media screen and (max-width: $media-mobile-sm-min) {
          padding: 8px 16px;
          justify-content: flex-start;
          align-items: center;
          @include flexbox;
          @include linearGradient(
            90deg,
            rgba(#006231, 0.2) 20.58%,
            rgba(#006231, 0) 42.84%
          );
        }
      }

      .df-pitch__card {
        padding: 0;

        @media screen and (max-width: $media-mobile-sm-min) {
          justify-content: flex-start;

          .df-pitch__plyr {
            margin: 0;
            flex-basis: calc(100% / 3);
            width: calc(100% / 3);
            text-align: left;
          }
        }
      }
    }

    &-tabs {
      background: $white;
      margin-top: 16px;

      @media screen and (max-width: $media-mobile-sm-min) {
        margin-top: 0;
      }

      ul {
        white-space: nowrap;
        justify-content: space-between;
        align-items: center;
        @extend %flexbox !optional;

        li {
          padding: 0 16px;
          flex-basis: 100%;
          text-align: center;
          font-size: 14px;
          color: rgba($client-primary-color, 0.6);
          @extend .df-regularFF !optional;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 20px;
          }

          em {
            color: $client-primary-color;
          }

          span {
            display: inline-block;
            padding: 8px 4px 12px;
            position: relative;
          }
        }
      }
    }

    &-txt {
      color: $white;
      font-size: 12px;
      @extend .df-boldFF !optional;
    }

    &-num {
      font-size: 24px;
      color: $white;
      @extend .df-borRad100 !optional;
      @extend .df-boldFF !optional;

      @media screen and (max-width: $media-mobile-sm-min) {
        background-color: rgba($white, 0.2);
        margin-left: 8px;
        font-size: 12px;
        padding: 2px 8px;
      }
    }

    &-teamName {
      font-size: 12px;
      padding: 12px 20px;
      color: $white;
      justify-content: space-between;
      align-items: baseline;
      @extend %flexbox !optional;
      background-color: rgba($black, 0.2);

      @media screen and (min-width: $media-mobile-max) {
        padding: 16px 32px;
      }

      em {
        margin-left: 4px;
        width: 20px;
        height: 20px;
        padding: 2px;
        background-color: rgba($white, 0.24);
        justify-content: center;
        align-items: center;
        @extend %inline-flex !optional;
        @extend .df-borRad4 !optional;
      }
    }
  }

  &__invite {
    &-head {
      padding: 16px;
      background-color: $client-primary-color;
      // background: url(../images/Create-contest-bg@3x.jpg?v=#{$image-version}) $client-secondary-color no-repeat top right;
      min-height: 128px;
      margin-top: -12px;
      color: $white;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      text-align: center;
      @extend %flexbox !optional;
      // @include background-size(contain);
    }

    &-body {
      text-align: center;
      padding: 24px 16px;
    }

    &-title {
      font-size: 20px;
      margin-bottom: 4px;
      @extend .df-boldFF !optional;

      &-subs {
        font-size: 12px;
        @extend .df-regularFF !optional;
      }
    }
  }

  &__share {
    &-bar,
    &-lbl {
      color: $disabled-text;
      font-size: 12px;
      @extend .df-regularFF !optional;
      margin-bottom: 10px;
    }

    &-code {
      padding: 10px;
      border: 1px dashed #95a1a8;
      color: $client-secondary-color;
      font-size: 20px;
      @extend .df-blackBoldFF !optional;
      justify-content: center;
      align-items: center;
      @extend %inline-flex !optional;
      @extend .df-borRad4 !optional;

      i {
        cursor: pointer;
        margin-left: 10px;
      }
    }

    &-clipboard {
      margin-top: 16px;
      font-size: 12px;
      @extend .df-regularFF !optional;
      color: $client-secondary-color;

      span {
        cursor: pointer;
        justify-content: center;
        align-items: center;
        @extend %flexbox !optional;

        i {
          font-size: 16px;
          margin-left: 8px;
        }
      }
    }
  }

  &__thumb {
    cursor: pointer;
    background: no-repeat center center;
    overflow: hidden;
    flex-shrink: 0;
    margin-right: 16px;
    width: 52px;
    height: 52px;
    @extend .df-borRad100 !optional;
    @include background-size(cover);
  }

  &__title {
    flex-basis: 100%;
    cursor: pointer;

    span {
      display: block;
      @extend .df-regularFF !optional;
      flex-basis: 100%;
      color: $client-primary-color;

      em {
        @extend .df-boldFF !optional;
      }

      &:first-child {
        font-size: 20px;
        color: $client-primary-color;
        @extend .df-boldFF !optional;
      }

      &:last-child {
        font-size: 12px;
        @extend .df-regularFF !optional;
        cursor: pointer;
      }
    }
  }

  &__shareIcon {
    color: $client-primary-color;
    font-size: 16px;
    cursor: pointer;
    width: calc(32px + 4px * 2);
    height: calc(32px + 4px * 2);
    flex-shrink: 0;
    justify-content: center;
    align-items: flex-start;
    margin-right: -16px;
    @extend %flexbox !optional;
  }

  &__dateLine {
    color: $client-primary-color;
    padding: 16px;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;

    span {
      font-size: 14px;

      &:last-child {
        text-align: right;
        cursor: pointer;
      }

      em {
        font-size: 16px;
        display: block;
        @extend .df-boldFF !optional;

        i {
          margin-left: 4px;
        }
      }
    }
  }

  &__matchPts {
    font-size: 14px;
    color: $white;
    padding-left: 12px;
    @extend .df-boldFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }

    span {
      padding-left: 16px;
      position: relative;

      &::after {
        content: "";
        width: 4px;
        height: 4px;
        position: absolute;
        left: 0;
        top: 50%;
        background: #aeaeae;
        @include transform(translateY(-50%));
        @extend .df-borRad100 !optional;
      }
    }
  }

  &__text {
    font-size: 14px;
    padding: 0 16px 16px;
    color: $client-primary-color;
    @extend .df-regularFF !optional;

    em {
      @extend .df-boldFF !optional;
    }
  }

  &__viewMore {
    // padding-right: 10px;
    position: relative;

    &.df-dark--tooltip {
      padding-top: 2px;

      &:hover {
        @media screen and (min-width: $media-mobile-max) {
          .df-contest__viewMore-menu {
            pointer-events: all;
            z-index: 11;
            opacity: 1;
            @include transform(translateY(0em));
          }
        }
      }

      .df-contest__viewMore-icon {
        width: auto;
        font-size: 14px;
        margin-left: 2px;
      }

      .df-contest__viewMore-menu {
        background-color: #7d879f;
        color: $white;
        right: calc(100% - 36px);
        min-width: 128px;
        margin-top: 8px;

        @media screen and (min-width: $media-mobile-max) {
          min-width: 150px;
        }

        &::after {
          content: "";
          position: absolute;
          top: -5px;
          right: 22px;
          left: auto;
          width: 10px;
          height: 10px;
          background-color: #7d879f;
          @include transform(rotate(45deg));
        }
      }
    }

    &.df-active {
      .df-contest__viewMore-menu {
        pointer-events: all;
        z-index: 11;
        opacity: 1;
        @include transform(translateY(0em));
      }
    }

    &-text {
      span {
        color: $white;
        display: block;
      }
    }

    &-icon {
      text-align: right;
      width: 10px;
      cursor: pointer;
      color: $client-primary-color;
    }

    &-menu {
      position: absolute;
      top: auto;
      right: 0;
      left: auto;
      padding: 8px;
      background: $white;
      z-index: -9999;
      pointer-events: none;
      opacity: 0;
      @include box-shadow(0, 2px, 6px, 0, rgba($black, 0.2));
      @include border-radius(4px);
      @include transform(translateY(1em));
      @include transition(transform 0.2s ease-in-out);

      ul {
        li {
          cursor: pointer;
          padding: 0 !important;
          background: transparent !important;
          border: none !important;
          color: $client-primary-color;
          font-size: 14px;
          @extend .df-regularFF !optional;
          justify-content: flex-start;
          align-items: center;
          @extend %flexbox !optional;

          &:not(:last-child) {
            margin-bottom: 16px;
          }
        }
      }
    }

    &-lft {
      margin-right: 8px;
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      background-color: $very-light-purple;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
      @extend .df-borRad100 !optional;
    }

    &-rgt {
      white-space: nowrap;
    }
  }
}

// leaderboard
.df-lBoard {
  &__tbl {
    .df-plyrSel {
      &__list {
        padding-bottom: 32px;

        &.df-CompareTeam {
          ul li {
            background: $light-blue-grey-two;
            border-bottom-color: $light-blue-grey;

            &.df-remove {
              background: $duck-egg-blue-two;
              opacity: 0.4;
              cursor: default;
            }
          }
        }

        ul li {
          padding: 12px 0;
          &.df-remove {
            background-color: $row-selected;
          }
          &.df-disabled {
            background-color: rgba($duck-egg-blue, 0.6);
          }
        }
      }

      &__wrap {
        .df-tbl {
          padding: 0 12px;

          &__head {
            .df-tbl__cell {
              &--name,
              &--pts,
              &--amt {
                cursor: default;
              }
            }
          }

          &__body {
            .df-tbl__cell {
              &--name {
                justify-content: flex-start;
                align-items: center;
                @extend %flexbox !optional;
              }

              &--pts {
                color: rgba($client-primary-color, 0.6);
              }

              &--amt {
                color: $client-primary-color;
                em {
                  color: $client-primary-color;
                  font-family: $regular_font-family;
                  font-weight: $regular_font-weight;
                }
              }
            }
          }

          &__cell {
            &--pts {
              width: 100px;
              @extend .df-regularFF !optional;
            }

            &--amt {
              width: 80px;

              .df-contest__viewMore {
                padding-left: 10px;
                display: inline-block;

                &-icon {
                  color: #7d879f;
                }
              }
            }

            &--pts,
            &--amt {
              padding-right: 4px;
              text-align: right;

              @media screen and (min-width: $media-mobile-max) {
                width: 100px;
              }
            }
          }
        }
      }

      &__thumb {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        margin-right: 8px;
        margin-bottom: 0;
        @include background-size(cover);
        @extend .df-borRad100 !optional;
      }

      &__name {
        overflow: hidden;
        color: $client-primary-color;
        padding-right: 0;
        @extend .df-regularFF !optional;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: baseline;
        @extend %flexbox !optional;
        em {
          margin-left: 4px;
          width: 20px;
          height: 20px;
          padding: 2px;
          @extend .df-boldFF !optional;
          color: $white;
          font-size: 10px;
          background-color: $progress-color;
          justify-content: center;
          align-items: center;
          @extend %inline-flex !optional;
          @extend .df-borRad4 !optional;
        }
      }
    }
  }
}

// player stats
.df-playerStats {
  &__wrap {
    &-nav {
      .df-playerStats__box {
        padding-bottom: 84px;

        @media screen and (max-width: $media-mobile-sm-min) {
          padding-bottom: 104px;
        }
      }

      .df-playerStats__legends {
        bottom: 52px;
      }
    }
    &-noNav {
      .df-playerStats__box {
        padding-bottom: 32px;
      }
      .df-playerStats__legends {
        bottom: 0;
      }
    }
  }

  &-rowHead {
    font-size: 12px;
    color: rgba($client-primary-color, 0.6);
    @extend .df-regularFF !optional;
    &.df-active {
      &--up {
        span {
          &::after {
            @include transform(translateY(-50%) rotateX(180deg));
          }
        }
      }

      &--dw {
        span {
          &::after {
            @include transform(translateY(-50%));
          }
        }
      }

      &--up,
      &--dw {
        span {
          cursor: pointer;
          display: inline-block;
          color: rgba($client-primary-color, 0.6);
          position: relative;

          &::after {
            content: "\e908";
            font-family: "dfi" !important;
            color: $red;
            position: absolute;
            left: auto;
            right: -12px;
            top: 50%;
            width: 12px;
            height: 12px;
            font-size: 10px;
            justify-content: center;
            align-items: center;
            @extend %flexbox !optional;
          }
        }
      }
    }
  }

  &-rowBody {
    min-height: 41px;
    font-size: 14px;
    color: $client-primary-color;
    @extend .df-regularFF !optional;
  }

  &-tab {
    margin: 0px -16px;
    padding: 10px;
    background: $duck-egg-blue;

    .df-cmo__info {
      border-bottom: none;
      background: transparent;
      padding: 0;
    }

    .df-cmo__tabs {
      overflow: hidden;
      padding: 0;
      margin: 0;
      @include box-shadow(0, 0, 0, 0, transparent);
      @extend .df-borRad100 !optional;

      ul {
        position: relative;
        z-index: 1;

        li {
          flex-grow: 1;
          padding: 0;
          color: $cta-primary-color-new;
          font-size: 12px;
          @extend .df-regularFF !optional;

          &.df-active {
            color: $white;

            span {
              &::after {
                background-color: $cta-primary-color-new;
                z-index: -1;
                height: 100%;
                @extend .df-borRad100 !optional;
              }
            }
          }

          span {
            padding: 10px 16px;

            &::after {
              height: 100%;
              @include transition(none 0.3s ease-in);
            }
          }
        }
      }

      &-underline {
        height: 100%;
        @extend .df-borRad100 !optional;
      }
    }
  }

  &__box {
    padding-bottom: 4px * 8;
    overflow: hidden;
    width: 100%;
    border-top: 1px solid #e7e7e7;
    background: $white;
    justify-content: flex-start;
    align-items: flex-start;
    @extend %flexbox !optional;

    @media screen and (max-width: $media-mobile-sm-min) {
      padding-bottom: 50px;
    }

    &-lft,
    &-rgt {
      position: relative;

      ul {
        li {
          position: relative;
          justify-content: flex-start;
          align-items: center;
          @extend %flexbox !optional;

          &::after {
            content: "";
            position: absolute;
            top: auto;
            bottom: 0;
            left: 0;
            width: 100vh;
            height: 1px;
            background: #e7e7e7;
          }
        }
      }
    }

    &-lft {
      border-right: 1px solid #e7e7e7;
      flex-basis: 200px;
      flex-shrink: 0;

      .df-playerStats-rowBody {
        justify-content: flex-start;
        align-items: flex-end;
      }

      .df-playerStats-rowHead {
        span {
          padding: 8px 16px;
        }
      }

      .df-transfer__plyr {
        padding-left: 4px;
        position: relative;
        margin: 0;
        border-bottom: none;
        width: 100%;
        cursor: pointer;
        min-height: 44px;
        overflow: hidden;

        &.df-unavailable,
        &.df-injured {
          .df-transfer__plyr-name {
            position: relative;
            display: inline-grid;
            padding-right: 16px;

            &::after {
              content: "";
              width: 12px;
              height: 12px;
              background-position: center center;
              background-repeat: no-repeat;
              position: absolute;
              left: auto;
              right: 0;
              top: 50%;
              @include transform(translateY(-50%));
              @extend .df-bgSize--contain !optional;
            }

            span {
              @include text-truncate;
            }
          }
        }

        &.df-unavailable {
          .df-transfer__plyr-name {
            &::after {
              background-image: url(../images/unavailable.svg?v=#{$image-version});
            }
          }
        }

        &.df-injured {
          .df-transfer__plyr-name {
            &::after {
              background-image: url(../images/injured.svg?v=#{$image-version});
            }
          }
        }
        &.df-in {
          .df-transfer__plyr-name {
            position: relative;
            display: inline-grid;
            padding-right: 16px;

            &::after {
              content: "";
              width: 6px;
              height: 6px;
              background-color: $status-green;
              position: absolute;
              left: auto;
              right: 0;
              top: 50%;
              @include transform(translateY(-50%));
              @extend .df-borRad100 !optional;
            }

            span {
              @include text-truncate;
            }
          }
        }

        &-skill {
          @extend .df-mediumFF !optional;

          em {
            @extend .df-regularFF !optional;
            padding-left: 4px;
            color: $disabled-text;
          }
        }

        &-thumb {
          height: 36px;
          width: 46px;
          position: relative;
          flex-shrink: 0;
          margin-bottom: -6px;
        }
      }

      .df-plyrSel__status {
        left: 0;
        top: 0;
        bottom: auto;

        .df-plyrSel__flight {
          width: 12px;
        }
      }
    }

    &-rgt {
      overflow: hidden;
      flex-basis: 100%;
      .swiper-slide {
        width: auto;
      }
      .df-playerStats-rowHead,
      .df-playerStats-rowBody {
        white-space: nowrap;

        span {
          margin: 8px 16px;
          // min-width: 50px;
          width: 100%;
          text-align: right;
        }
      }

      .df-playerStats-rowBody {
        min-height: 44px;

        span {
          position: relative;

          &.df-in {
            &::after {
              content: "";
              position: absolute;
              left: auto;
              right: -6px;
              top: -6px;
              width: 8px;
              height: 8px;
              background: $green;
              border: 1px solid $white;
              @extend .df-borRad100 !optional;
            }
          }
        }
      }

      &-scroll {
        width: 100%;
        overflow: hidden;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
      }
    }
  }

  &__legends {
    z-index: 2;
    position: fixed;
    top: auto;
    bottom: 0;
    left: 0;
    max-width: 550px;
    width: 100%;
    padding: 4px 16px;
    background: rgba(#31353e, 0.95);

    ul {
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      li {
        padding-top: 4px;
        padding-bottom: 4px;
        font-size: 10px;
        color: #dddfe1;
        @extend .df-regularFF !optional;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 12px;
        }

        &:not(:last-child) {
          padding-right: 20px;

          @media screen and (min-width: $media-mobile-max) {
            padding-right: 24px;
          }
        }
      }
    }

    &-icon {
      justify-content: center;
      align-items: center;
      width: 10px;
      height: 10px;
      flex-basis: 12px;
      flex-shrink: 0;
      margin-right: 4px;
      @extend %flexbox !optional;

      @media screen and (min-width: $media-mobile-max) {
        width: 12px;
        height: 12px;
      }

      img {
        display: block;
        // width: 100%;
      }

      @media screen and (min-width: $media-mobile-max) {
        width: 16px;
        height: 16px;
        flex-basis: 16px;
      }
    }

    &-text {
      flex-shrink: 0;
    }

    &-dot {
      background: $green;
      width: 6px;
      height: 6px;
      @extend .df-borRad100 !optional;
    }
  }
}

// compare
.df-compare {
  &__wrap {
    .df-noDataFound {
      padding-top: 16px;
    }
    &--head {
      .df-compare__box {
        margin: 0 -16px;
        padding: 16px;
        &-title {
          @extend .df-boldFF !optional;
        }
      }
    }
  }

  &__head {
    padding: 16px;
  }

  &__title {
    padding-top: 16px;
    text-align: center;
    color: $client-primary-color;
    @extend .df-boldFF !optional;
  }

  &__box {
    padding: 16px 0;
    background: $white;
    margin-top: 12px;
    @extend .df-boxShadow--sm !optional;

    &-title {
      text-align: center;
      color: $client-primary-color;
      @extend .df-regularFF !optional;

      em {
        @extend .df-boldFF !optional;
      }
    }

    .df-transfer__plyr {
      &.df-triBoos,
      &.df-cap,
      &.df-vcap {
        .df-transfer__plyr-thumb {
          position: relative;

          &::after {
            color: $default;
            position: absolute;
            left: 4px;
            top: 4px;
            width: 16px;
            height: 16px;
            background: $white;
            border: 1px solid $default;
            font-size: calc(100% - 8px);
            line-height: 0;
            @extend .df-boldFF !optional;
            justify-content: center;
            align-items: center;
            @extend .df-borRad100 !optional;
            @extend %flexbox !optional;
          }
        }
      }

      &.df-cap {
        .df-transfer__plyr-thumb {
          &::after {
            content: "C";
          }
        }
      }

      &.df-vcap {
        .df-transfer__plyr-thumb {
          &::after {
            content: "VC";
          }
        }
      }

      &.df-triBoos {
        .df-transfer__plyr-thumb {
          &::after {
            content: "\e91e";
            font-family: "dfi" !important;
            font-size: 10px;
          }
        }
      }
    }

    .df-transfer__tbl .df-tbl__cell--lft,
    .df-transfer__tbl .df-tbl__cell--rgt {
      .df-transfer__plyr-name {
        color: $client-primary-color;
      }
      .df-transfer__plyr-skill {
        color: rgba($client-primary-color, 0.48);
      }
    }
    .df-transfer__tbl .df-tbl__cell--lft {
      border-right: none;
    }
  }

  &__row {
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;

    &:not(:first-child) {
      padding: 8px 0;
      border-bottom: 1px solid rgba(#e4e4e4, 0.5);

      .df-compare__row-mid {
        color: $steel;
      }
    }

    // &:nth-child(2) {
    //   border-bottom: 1px solid rgba(#e4e4e4, 0.5);
    // }

    // &:nth-child(3) {
    //   padding-bottom: 0;
    // }

    &-lft,
    &-rgt {
      flex-basis: 100%;
      color: $client-primary-color;
      @extend .df-regularFF !optional;
      font-size: 14px;

      .df-plyrSel {
        &__thumb {
          overflow: hidden;
          flex-shrink: 0;
          margin-bottom: 0;
          width: 48px;
          height: 48px;
          @extend .df-borRad100 !optional;
        }

        &__name {
          font-size: 16px;
          padding-left: 4px;
          padding-right: 4px;
        }
      }
    }

    @extend .df-boldFF !optional;

    &-mid {
      font-size: 12px;
      text-align: center;
      flex-basis: 100px;
      flex-shrink: 0;
      color: $client-primary-color;
      @extend .df-regularFF !optional;
    }

    &-lft {
      .df-pitch__booster {
        justify-content: flex-start;
      }
    }

    &-rgt {
      text-align: right;

      .df-pitch__booster {
        justify-content: flex-end;
      }

      .df-plyrSel__dtls {
        flex-direction: row-reverse;
      }
    }
  }

  &__booster {
    font-size: 12px;
    color: $client-primary-color;
    @extend .df-regularFF !optional;

    .df-pitch__booster {
      @extend .df-pitch__booster--overwrite;

      .df-cmo__pickPlayer-booster-icon {
        width: 24px;
        height: 24px;
      }
    }
  }
  &__header {
    justify-content: space-between;
    align-items: flex-start;
    @extend %flexbox !optional;

    &-lft,
    &-rgt {
      flex-basis: 100%;
      flex-grow: 1;
    }
    &-lft {
      text-align: left;
    }
    &-rgt {
      text-align: right;
    }
    &-title {
      font-size: 14px;
      color: $default;
      @extend .df-regularFF !optional;
    }
    &-cta {
      margin: 8px 0;
      display: inline-block;
      .df-download__cta {
        i {
          margin-left: 8px;
          margin-right: 0;
        }
      }
    }
    &-pts {
      font-size: 12px;
      @extend .df-mediumFF !optional;
      color: $neutral-darker;
    }
  }
  &__totalPts {
    margin-bottom: 12px;
    &-title {
      font-size: 12px;
      text-align: center;
      margin-bottom: 6px;
      color: $neutral-medium;
      @extend .df-regularFF !optional;
    }
    &-box {
      max-width: 180px;
      width: 100%;
      margin: 0 auto;
      position: relative;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
      &::after,
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0;
        top: 0;
        background: linear-gradient(
          270deg,
          $white 3.87%,
          $cta-primary-color 39.95%,
          $cta-primary-color 57.84%,
          $white 96.68%
        );
      }
      &::before {
        bottom: 0;
        top: auto;
      }
    }
    &-lft,
    &-rgt {
      margin: 0 1px;
      padding: 0 24px;
      position: relative;
      @include transform(skew(-30deg));
      &::after {
        content: "";
        position: absolute;
        height: 50%;
        width: 2px;
        background-color: $cta-primary-color;
      }
      span {
        color: $neutral-darker;
        @extend .df-boldFF !optional;
        font-size: 16px;
        display: block;
        padding: 8px 0;
        @include transform(skew(30deg));
      }
    }
    &-lft {
      &.df-won {
        background: linear-gradient(
          90deg,
          rgba(154, 61, 190, 0) 6.55%,
          rgba(154, 61, 190, 0.1) 50%
        );
      }
      &::after {
        right: 0;
        left: auto;
        top: 0;
      }
    }
    &-rgt {
      &.df-won {
        background: linear-gradient(
          270deg,
          rgba(154, 61, 190, 0) 6.55%,
          rgba(154, 61, 190, 0.1) 50%
        );
      }
      &::after {
        left: 0;
        top: auto;
        bottom: 0;
      }
    }
  }
}

// announcement notify
.df-announcement {
  &__wrap {
    margin: 0 -16px;
    padding: 8px 16px;
    justify-content: flex-start;
    align-items: center;
    background-color: $pale;
    @extend %flexbox !optional;
    // @include linearGradient(90deg, #ffe6a5 8.54%, #ffdb6b 62.24%);
  }

  &__icon {
    color: $status;
    margin-right: 16px;
    font-size: 20px;
  }

  &__text {
    font-size: 12px;
    color: $black;
    @extend .df-regularFF !optional;

    em {
      @extend .df-boldFF !optional;
    }
  }
}

// score card (ULC)
.df-scoreBoard {
  overflow: hidden;
  // background: $client-secondary-color;
  margin: 12px auto;
  @include transition(height 0.1s linear, margin 0.1s linear);

  &__box {
    width: 100%;
    min-height: 90px;
    padding: 12px;
    overflow: hidden;
    // background: $french-blue;
    background-color: $white;
    @extend .df-borRad8 !optional;

    @media screen and (min-width: $media-mobile-max) {
      min-height: 104px;
    }

    // &:first-child {
    //   margin-left: 16px;
    // }

    // &:last-child {
    //   margin-right: 16px !important;
    // }
    &.df-match__box {
      background-color: transparent;
      box-shadow: none;
    }

    &.df-upcoming {
      .df-scoreBoard__lbl {
        &::after {
          background: $red;
        }
      }
      // .df-scoreBoard__lbl {
      //   display: none;
      // }
    }

    &.df-abandoned {
      .df-scoreBoard__status {
        justify-content: center;
      }
      .df-scoreBoard__lbl {
        background-color: $status;
        color: $white;
      }
    }
    &.df-upcoming,
    &.df-abandoned,
    &.df-review,
    &.df-completed,
    &.df-live {
      .df-scoreBoard__lbl {
        padding-left: 16px;
        padding-right: 6px;
        padding-top: 2px;
        padding-bottom: 2px;
        &::after {
          content: "";
          position: absolute;
          left: 6px;
          top: 50%;
          width: 6px;
          height: 6px;
          @include transform(translateY(-50%));
          @extend .df-borRad100 !optional;
        }
      }
    }

    &.df-live {
      .df-scoreBoard__lbl {
        &::after {
          background: $red;
        }
      }
    }

    &.df-completed {
      .df-scoreBoard__lbl {
        color: $green;
        &::after {
          background: $green;
        }
      }
    }

    &.df-review {
      .df-scoreBoard__lbl {
        color: #3f8efc;
        &::after {
          background-color: #3f8efc;
        }
      }
    }

    &.df-abandoned {
      .df-scoreBoard__lbl {
        @extend .df-borRad8 !optional;
        &::after {
          background: $white;
        }
      }
    }
  }

  &__status {
    font-size: 12px;
    color: $light-grey-blue;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }
  }

  &__lbl {
    // padding: 2px 8px;
    position: relative;
    font-size: 10px;
    color: $status;
    // background: $faded-blue;
    // @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }

  &__status,
  &__teamName {
    @include transition(transform 0.2s linear, opacity 0.2s linear);
  }

  &__teamName {
    font-size: 12px;
    color: rgba($client-primary-color, 0.6);
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }
  }

  &__fix {
    padding: 4px 0 8px;

    .df-scoreBoard__status {
      display: none;
    }

    .df-fix__team {
      justify-content: space-between;

      &-A,
      &-B {
        width: auto;

        &.df-loss {
          .df-fix__team-name {
            color: $light-blue-grey;
          }
        }
      }

      &-B {
        flex-direction: row-reverse;
      }

      &-vs {
        color: $client-primary-color;
        flex-basis: 90px;
        @include transition(transform 0.2s linear, opacity 0.2s linear);
      }

      &-name {
        margin: 0px 8px;
        // color: $white;

        @media screen and (max-width: $media-mobile-max) {
          font-size: 12px;
        }

        em {
          @extend .df-regularFF !optional;
        }
      }

      &-logo {
        width: 32px;
        height: 32px;

        @media screen and (max-width: $media-mobile-max) {
          width: 24px;
          height: 24px;
        }
      }
    }
  }

  &--v2 {
    .df-scoreBoard {
      &__box {
        background-color: $white;
        min-height: auto;
        &.df-live {
          // .df-fix__team-vs {
          //   display: none;
          // }

          // .df-scoreBoard__status:last-child {
          //   // @include flexbox;
          //   visibility: hidden;
          //   display: none;
          // }
        }

        &.df-upcoming {
          .df-scoreBoard__fix .df-fix__team-vs {
            flex-basis: 110px;

            @media screen and (min-width: $media-mobile-max) {
              flex-basis: 140px;
            }
          }

          .df-scoreBoard__status,
          .df-scoreBoard__lbl {
          }

          // .df-scoreBoard__status .df-scoreBoard__lbl {
          //   margin-top: -30px;
          //   visibility: visible;
          //   @extend .df-borRad0 !optional;

          //   @media screen and (min-width: $media-mobile-max) {
          //     margin-top: -40px;
          //   }
          // }
        }

        &.df-abandoned {
          // .df-fix__team-vs {
          //   display: none;
          // }

          .df-scoreBoard__status {
            span:first-child {
              visibility: hidden;
              display: none;
            }
          }
        }
      }

      &__fix {
        padding-bottom: 8px;
        // .df-fix__team-vs {
        //   align-self: flex-start;
        // }
      }

      &__teamName {
        margin-bottom: 4px;
        color: rgba($client-primary-color, 0.6);
      }

      &__status {
        color: rgba($client-primary-color, 0.6);
        // justify-content: center;

