  background: $teal;
  color: $white;
  font-size: 10px !important;
  font-family: $regular_font-family !important;
  font-weight: $regular_font-weight !important;
  @extend .df-borRad100 !optional;
}

.df-scrUpdate {
  flex-grow: 1;
  font-size: 12px;
