// Point System
.df-ptSyt {
  &-list {
    padding: 0 16px;
  }

  ul {
    li {
      // padding: 12px 16px;
      color: $text-dark;
      @extend .df-regularFF !optional;
      font-size: 14px;
      justify-content: space-between;
      align-items: stretch;
      @extend %flexbox !optional;
      border-bottom: 1px solid rgba($black, 0.1);

      span {
        flex-basis: 100%;
        padding: 12px 16px;

        &:last-child {
          flex-basis: 62px;
          text-align: right;
          background-color: $green;
          color: $white;
          @extend .df-boldFF !optional;
          justify-content: flex-end;
          align-items: center;
          @extend %flexbox !optional;
        }
        em {
          display: block;
          font-size: 10px;
          color: $disabled-text;
          @media screen and (min-width: $media-mobile-max) {
            font-size: 12px;
          }
        }
      }
    }
  }
  &__content {
    padding: 16px;
    p {
      font-size: 12px;
      line-height: 1.5;
      color: $black;
      @extend .df-regularFF !optional;
      &:not(:last-child) {
        margin-bottom: 8px;
        border-bottom: none;
      }
    }
    .df-ptSyt__notes {
      padding-top: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__thumb {
    img {
      display: block;
      width: 100%;
    }
  }
  &__notes {
    padding: 16px;

    &-title {
      font-size: 12px;
      color: $black;
      @extend .df-boldFF !optional;
      margin-bottom: 8px;
    }

    ul {
      list-style: disc outside;
      margin-left: calc(16px + 8px);

      li {
        background-color: transparent;
        border-bottom: none;
        font-size: 12px;
        line-height: 1.5;
        color: $text-dark;
        display: list-item;
        padding: 0;
        padding-bottom: 4px;
        @extend .df-regularFF !optional;

        &:not(:last-child) {
          margin-bottom: 8px;
          border-bottom: none;
        }
        ul {
          list-style: lower-alpha outside;
        }
      }
    }
  }
  .df-accordion__box {
    padding-left: 0;
    padding-right: 0;

    &.df-active {
      .df-accordion__head {
        padding-bottom: 12px;

        // &::after {
        //   @include transform(translateY(-50%) rotateX(180deg));
        // }
      }
    }
  }

  .df-accordion__head {
    color: $client-primary-color;
    @extend .df-boldFF !optional;
    font-size: 14px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 0;

    &::after {
      height: auto;
      // top: 50%;
      bottom: auto;
      right: 16px;
      // @include transform(translateY(-50%));
    }

    em {
      display: block;
      font-size: 10px;
      @extend .df-regularFF !optional;
    }
  }

  .df-accordion__body {
    padding-top: 0;
  }
}

// Prizes
.df-prize {
  &__box {
    text-align: center;
    padding: 20px 16px;
    background: #fff;
    @extend .df-boxShadow--sm !optional;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__header {
    position: relative;
    display: inline-block;
    margin: 0 auto;
    min-width: 100px;
    min-height: 40px;
    text-align: center;

    &-text {
      &--1 {
        color: $steel;
        font-size: 12px;
        @extend .df-regularFF !optional;
      }

      &--2 {
        color: $dark-indigo;
        font-size: 20px;
        @extend .df-boldFF !optional;
      }
    }

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-repeat: no-repeat, no-repeat;
      background-position: left center, right center;
      @extend .df-bgSize--contain !optional;
    }

    &.df-first {
      &::after {
        background-image: url(../images/golden-leaf--lft.svg?v=#{$image-version}),
          url(../images/golden-leaf--right.svg?v=#{$image-version});
      }
    }

    &.df-second {
      &::after {
        background-image: url(../images/silver-leaf--left.svg?v=#{$image-version}),
          url(../images/silver-leaf--right.svg?v=#{$image-version});
      }
    }

    &.df-third {
      &::after {
        background-image: url(../images/bronze-leaf--left.svg?v=#{$image-version}),
          url(../images/bronze-leaf--right.svg?v=#{$image-version});
      }
    }
  }

  &__list {
    margin-top: 20px;
    padding: 0 16px;
    background-color: rgba($cta-primary-color, 0.05);
    @extend .df-borRad8 !optional;

    ul {
      li {
        padding: 12px 0;
        justify-content: flex-start;
        align-items: center;
        @extend %flexbox !optional;

        &:not(:last-child) {
          border-bottom: 1px solid $light-blue-grey-two;
        }
      }
    }

    &-thumb {
      flex-basis: 20px;
      flex-shrink: 0;
      margin-right: 20px;
      &--img {
        flex-basis: 80px;
        img {
          width: 100%;
        }
      }
    }

    &-details {
      text-align: left;
      flex-basis: 100%;

      &--one {
        color: $client-primary-color;
        font-size: 14px;
        @extend .df-boldFF !optional;
      }

      &--two {
        color: rgba($client-primary-color, 0.6);
        font-size: 12px;
        @extend .df-regularFF !optional;
      }
    }
  }
}

.df-uncapped {
  &--strip {
    background-color: $light-blue-grey-two;
    color: $client-primary-color;
    font-size: 10px;
    padding: 8px 16px;
    margin: 10px auto;
    justify-content: center;
    align-items: center;
    position: relative;
    left: 50%;
    @include transform(translateX(-50%));
    @extend .df-borRad100 !optional;
    @extend .df-regularFF !optional;
    @extend %inline-flex;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }

    i.dfi-uncap {
      margin-right: 8px;

      &::before {
        content: "";
        display: block;
        width: 16px;
        height: 16px;
        background: url(../images/uncapped.svg?v=#{$image-version}) no-repeat
          center center;
        @extend .df-bgSize--contain !optional;
      }
    }
  }
}

// booster
.df-booster {
  &__list {
    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
  }

  &__box {
    min-height: 88px;
    cursor: pointer;
    padding: 20px;
    background-color: $white;
    border: 1px solid $light-blue-grey;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-borRad8 !optional;
    @include transition(background 0.3s ease-in-out);

    &.df-tmp {
      .df-booster {
        &__icon {
          background-image: url(../images/booster-icons/3x_blue-line.svg?v=#{$image-version});
        }
      }
    }

    &.df-dbs {
      .df-booster {
        &__icon {
          background-image: url(../images/booster-icons/2x_blue-line.svg?v=#{$image-version});
        }
      }
    }

    &.df-st {
      .df-booster {
        &__icon {
          background-image: url(../images/booster-icons/arrow_blue-line.svg?v=#{$image-version});
        }
      }
    }

    &.df-active,
    &.df-selected {
      &.df-tmp,
      &.df-dbs,
      &.df-st {
        .df-booster {
          &__title {
            @extend .df-boldFF !optional;
          }

          &__para {
            color: $client-primary-color;
          }

          &__radioBtn {
            &::after {
              @include transform(scale(1));
            }
          }
        }
      }

      &.df-tmp {
        @include linearGradient(
          180deg,
          rgba(255, 95, 66, 0.2) -8.52%,
          rgba($white, 0) 132.95%
        );

        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/3x_over-stroke.svg?v=#{$image-version});
          }
        }
      }

      &.df-dbs {
        @include linearGradient(
          180deg,
          rgba(197, 75, 177, 0.2) 0%,
          rgba($white, 0) 134.09%
        );

        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/2x_over-stroke.svg?v=#{$image-version});
          }
        }
      }

      &.df-st {
        @include linearGradient(
          180deg,
          rgba(0, 180, 157, 0.2) 0%,
          rgba($white, 0) 148.3%
        );

        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/arrow_over-stroke.svg?v=#{$image-version});
          }
        }
      }
    }

    &.df-selected {
      &.df-tmp,
      &.df-dbs,
      &.df-st {
        border-color: $client-secondary-color;
      }
    }

    &.df-active {
      &.df-tmp,
      &.df-dbs,
      &.df-st {
        cursor: default;
        border-color: transparent;
        border: none;
      }
    }

    &.df-disabled {
      cursor: default;
      pointer-events: none;
      @include linearGradient(
        180deg,
        #ecf0fa 0%,
        rgba(255, 255, 255, 0) 134.09%
      );

      .df-booster__title,
      .df-booster__para {
        color: $steel;
      }

      &.df-tmp {
        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/3x_hide-fill.svg?v=#{$image-version});
          }
        }
      }

      &.df-dbs {
        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/2x_hide-fill.svg?v=#{$image-version});
          }
        }
      }

      &.df-st {
        .df-booster {
          &__icon {
            background-image: url(../images/booster-icons/arrow_hide-fill.svg?v=#{$image-version});
          }
        }
      }
    }

    &.df-disabled,
    &.df-active {
      .df-booster__radioBtn {
        display: none;
      }
    }
  }

  &__icon {
    width: 36px;
    height: 36px;
    align-self: flex-start;
    flex-shrink: 0;
    background-repeat: no-repeat;
    background-position: center center;
    @extend .df-bgSize--contain !optional;
    @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      width: 42px;
      height: 42px;
    }
  }

  &__content {
    flex-basis: 100%;
    margin: 0 16px;
  }

  &__title {
    margin-bottom: 4px;
    font-size: 16px;
    color: $client-primary-color;
    @extend .df-mediumFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 18px;
    }
  }

  &__para {
    font-size: 12px;
    color: $steel;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      padding-right: 20%;
      font-size: 14px;
    }
  }

  &__note {
    margin-top: 10px;
    font-size: 10px;
    @extend .df-regularFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      padding-right: 20%;
      font-size: 12px;
    }
  }

  &__radioBtn {
    position: relative;
    width: 22px;
    height: 22px;
    flex-shrink: 0;
    background-color: $white;
    border: 2px solid $light-grey-blue;
    justify-content: center;
    align-items: center;
    align-self: flex-start;
    @extend %flexbox !optional;
    @extend .df-borRad100 !optional;

    &::after {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: $client-secondary-color;
      @include transition(transform 0.3s ease-in-out);
      @include transform(scale(0));
      @extend .df-borRad100 !optional;
    }
  }

  &__status {
    margin-top: 8px;
    font-size: 10px;
    padding: 4px 8px;
    border: 1px solid $light-blue-grey-three;
    background-color: $light-blue-grey-two;
    color: $client-primary-color;
    justify-content: center;
    align-items: center;
    @extend %inline-flex;
    @extend .df-regularFF !optional;
    @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }

    span {
      position: relative;

      &:first-child {
        padding-right: 10px;
        margin-right: 4px;

        @media screen and (min-width: $media-mobile-max) {
          padding-right: 14px;
          margin-right: 8px;
        }

        &::after {
          content: "";
          position: absolute;
          left: auto;
          right: -1px;
          top: 50%;
          width: 3px;
          height: 3px;
          background-color: $client-primary-color;
          @extend .df-borRad100 !optional;
          @include transform(translateY(-50%));

          @media screen and (min-width: $media-mobile-max) {
            width: 6px;
            height: 6px;
          }
        }
      }
    }

    em {
      @extend .df-boldFF !optional;
    }
  }
}

// df-performance__counter
.df-performance {
  &__counter {
    position: absolute;
    left: auto;
    right: 0;
    top: 50%;
    font-size: 10px;
    padding: 4px 8px;
    border: 1px solid $light-blue-grey-three;
    background-color: $light-blue-grey-two;
    @include transform(translateY(-50%));
    @extend .df-regularFF !optional;
    @extend .df-borRad100 !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }

    // em {
    //   @extend .df-boldFF !optional;
    // }
  }
}

.df-booster__icon-holder {
  position: relative;
  text-align: center;
  left: auto;
  top: auto;
  transform: none;
  margin-bottom: 15px;
  z-index: 1;
}

// Badge
.df-badge {
  &__wrap {
    .df-cmo__bck {
      flex-basis: auto;
    }
  }

  &__row {
    &:not(:last-child) {
      margin-bottom: 32px;
    }
  }

  &__list {
    margin-top: 16px;

    &-btn {
      text-align: center;

      .df-btn {
        @extend %inline-flex;
      }
    }

    ul {
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      li {
        cursor: pointer;
        margin-bottom: 24px;
        text-align: center;
        flex-basis: calc(100% / 3);

        .df-badge__box-thumb {
          width: 72px;
          height: 72px;
        }

        .df-badge__box-title {
          font-size: 14px;
          margin-bottom: 0;
          margin-top: 8px;
        }
      }
    }
  }

  &__swiper {
    overflow: hidden;

    &-wrap {
      overflow: hidden;
      justify-content: space-between;
      align-items: center;
      @extend %flexbox !optional;
    }

    .swiper-slide {
      // width: calc(100% - 32px * 2);
      // margin-left: 16px;
      &:first-child {
        padding-left: 16px;
      }

      &:last-child {
        padding-right: 16px;
      }
    }
  }

  &__noData {
    margin-top: 16px;

    .df-badge__box {
      display: block;
      text-align: center;

      &-lft {
        margin: 0 auto;
      }

      &-thumb {
        width: 90px;
        height: 90px;
        margin: 0 auto 24px;
      }

      &-caption {
        font-size: 14px;
        color: $steel;
      }
    }
  }

  &__head {
    width: 100%;
    padding: 8px 0 12px;
    align-items: center;
    justify-content: flex-start;
    @extend %flexbox !optional;

    &-lft {
      flex-shrink: 0;
      margin-right: 12px;
    }

    &-thumb {
      width: 32px;
      height: 32px;
      background-color: $light-grey-blue;
      background-position: center center;
      background-repeat: no-repeat;
      @extend .df-borRad100 !optional;
      @extend .df-bgSize--contain !optional;
    }

    &-rgt {
      flex-basis: 100%;
      letter-spacing: 0.15px;
    }

    &-userName {
      color: $white;
      font-size: 14px;
      @extend .df-boldFF !optional;
    }

    &-userLvl {
      font-size: 12px;
      color: $light-grey-blue;
      @extend .df-regularFF !optional;
    }
  }

  &__container {
    padding: 16px;
  }

  &__title {
    margin-bottom: 16px;
    text-align: center;
    font-size: 16px;
    color: $dark-indigo;
    @extend .df-boldFF !optional;

    span {
      position: relative;

      &::after,
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        width: 25px;
        height: 1px;
        border-top: 1px dashed $light-blue-grey-three;
        @include transform(translateY(-50%));
      }

      &::after {
        left: -30px;
      }

      &::before {
        right: -30px;
        left: auto;
      }
    }
  }

  &__box {
    cursor: pointer;
    padding: 16px 12px;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;
    @extend .df-borRad8 !optional;

    &:not(:last-child) {
      margin-bottom: 12px;
    }

    &.df-collector {
      .df-badge__box-thumb {
        width: 80px !important;
        height: 80px !important;
        position: relative;

        @media screen and (min-width: $media-mobile-max) {
          width: 100px !important;
          height: 100px !important;
        }

        &::after {
          content: "";
          width: 24px;
          height: 24px;
          position: absolute;
          top: 6px;
          left: auto;
          right: 0;
          background-image: url(../images/collector-ticked.svg?v=#{$image-version});
          background-position: center center;
          background-repeat: no-repeat;
          justify-content: center;
          align-items: center;
          @extend %flexbox !optional;
          @extend .df-bgSize--contain !optional;
        }
      }
    }

    &.df-earned {
      position: relative;
      background-color: $badg-earned;

      &::after {
        content: "";
        width: 28px;
        height: 28px;
        position: absolute;
        top: 6px;
        left: auto;
        right: 6px;
        background-image: url(../images/earned-ticked.svg?v=#{$image-version});
        background-position: center center;
        background-repeat: no-repeat;
        justify-content: center;
        align-items: center;
        @extend %flexbox !optional;
        @extend .df-bgSize--contain !optional;
      }

      .df-badge__box-title {
        color: $dark-aquamarine;
      }
    }

    &.df-locked {
      position: relative;

      &::after {
        content: "\e927";
        font-family: "dfi" !important;
        width: 24px;
        height: 24px;
        position: absolute;
        top: 8px;
        left: auto;
        right: 8px;
        font-size: 14px;
        background-color: green;
