      .df-badge__box-progress {
        &-bar,
        &-lbl {
          background-color: $badg-silver--dark;
        }
      }
    }

    &.df-bronze {
      background-color: $badg-bronze;

      &.df-locked {
        &::after {
          background-color: #f5dfd6;
        }
