      .df-badge__box-progress {
        &-bar,
        &-lbl {
          background-color: $badg-bronze--dark;
        }
      }
    }

    &.df-diamond {
      background-color: $badg-daimond;

      &.df-locked {
        &::after {
          background-color: $badg-daimond--dark;
        }
      }

      .df-badge__box-progress {
        &-bar,
        &-lbl {
          background-color: $badg-daimond--dark;
        }
      }
    }

    &.df-booster {
