    &-thumb {
      position: relative;
      z-index: 1;
      margin: 0 auto;
      width: 56px;
      height: 68px;
      background-position: center center;
      background-repeat: no-repeat;
      @extend .df-bgSize--contain !optional;
    }

    &-title {
      margin-bottom: 4px;
      font-size: 14px;
      color: $client-primary-color;
      @extend .df-boldFF !optional;

