    &-caption {
      margin-bottom: 4px;
      font-size: 12px;
      @extend .df-mediumFF !optional;
      color: $client-primary-color;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 14px;
      }
    }

    &-status {
      color: $steel;
      font-size: 10px;
      @extend .df-regularFF !optional;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 12px;
      }
    }

    &-progress {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      &-bar {
        position: relative;
        flex-basis: 100%;
        height: 3px;
        overflow: hidden;
        @extend .df-borRad100 !optional;

        span {
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          background-color: $teal;
          @extend .df-borRad100 !optional;
        }
      }

      &-lbl {
        margin-left: -4px;
        position: relative;
        z-index: 1;
        flex-shrink: 0;
        color: $steel;
        font-size: 10px;
        padding: 2px 8px;
        @extend .df-regularFF !optional;
        @extend .df-borRad100 !optional;
      }
    }

    &-info {
      text-align: center;
      font-size: 14px;
      color: $steel;
      @extend .df-regularFF !optional;
    }
  }

  &__tut {
    &-wrap {
      text-align: center;
      color: $white;
    }

    &-header {
      font-size: 16px;
      @extend .df-boldFF !optional;
      color: $client-primary-color;
      padding: 14px;
      border-bottom: 1px solid $light-blue-grey-two;
      align-items: center;
      justify-content: flex-start;
      @extend %flexbox !optional;

      i {
        font-size: 24px;
        margin-right: 14px;
      }
    }

    &-body {
      padding: 14px;

      .df-booster {
        &__box {
          background: none !important;
          padding: 0;
          min-height: auto;
          text-align: left;

          &.df-dbs {
            .df-booster__icon {
              background-image: url(../images/booster-icons/2x_white-border.svg?v=#{$image-version});
            }
          }

          &.df-st {
            .df-booster__icon {
              background-image: url(../images/booster-icons/arrow_white-border.svg?v=#{$image-version});
            }
          }

          &.df-tmp {
            .df-booster__icon {
              background-image: url(../images/booster-icons/3x_white-border.svg?v=#{$image-version});
            }
          }
        }

        &__content {
          margin: 0 0 0 8px;
        }

        &__title {
          font-size: 14px;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 16px;
          }
        }

        &__para {
          font-size: 12px;

          @media screen and (min-width: $media-mobile-max) {
            font-size: 14px;
          }
        }
      }
    }

    &-box {
      min-height: 300px;
      height: auto;
      _height: 300px;
      margin-top: 16px;
      opacity: 0.7;
      background-color: $white;
      @extend .df-borRad12 !optional;
      @include transition(opacity 0.1s linear);

      @media screen and (min-width: $media-mobile-max) {
        margin-top: 24px;
      }

      &.df-introducingBadges {
        .df-badge__tut {
          &-body {
            padding: 0 0 14px;
          }

          &-box-img {
            img {
              width: 100%;
            }
          }

          &-box-content {
            padding-left: 24px;
            padding-right: 24px;
          }
        }
      }

      &.df-adminControls {
        .df-badge__tut {
          &-body {
            padding: 0 0 14px;
          }

          &-box-img {
            img {
              width: 100%;
            }
          }

          &-box-content {
            padding-left: 24px;
            padding-right: 24px;
          }
        }
      }

      &-img {
        width: 100%;
        min-height: 90px;
        margin-bottom: 14px;
        justify-content: center;
        align-items: center;
        @extend %flexbox !optional;
      }

      &-content {
        font-size: 14px;
        color: $client-primary-color;
        @extend .df-regularFF !optional;
        line-height: 1.5em;

        em {
          display: block;
          font-size: 12px;
          color: $steel;
          @extend .df-regularFF !optional;
        }
      }
    }

    &-list {
      width: 100%;

      ul {
        justify-content: flex-start;
        align-items: flex-start;
        @extend %flexbox !optional;

        li {
          text-align: left;
          flex-basis: calc(100% / 4);
          flex-grow: 1;
        }
      }

      &Title {
        font-size: 10px;
        color: $steel;
        margin-bottom: 12px;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 12px;
        }
      }

      &Img {
      }
    }

    &-swiper {
      position: relative;

      .swiper-container {
        padding-bottom: 32px;
      }

      .swiper-button-next,
      .swiper-button-prev {
        margin-top: 0;
        width: 42px;
        height: 42px;
        @extend .df-bgSize--contain !optional;
      }

      .swiper-button-next {
        background-image: url(../images/swiper-next.png?v=#{$image-version});
      }

      .swiper-button-prev {
        background-image: url(../images/swiper-prev.png?v=#{$image-version});
      }

      .swiper-pagination {
        bottom: 0;
        margin: 0 auto;
        left: 0;
        right: 0;
        z-index: 2;

        &-bullet {
          width: 6px;
          height: 6px;
          background-color: $white;
          opacity: 1;
          margin-left: 2px !important;
          margin-right: 2px !important;
          @extend .df-borRad12 !optional;
          @include transition(width 0.1s linear);

          &-active {
            width: 24px;
            background-color: $white;
          }
        }
      }

      .swiper-slide-prev,
      .swiper-slide-next {
        .df-badge__tut-box {
          cursor: pointer;
        }
      }

      .swiper-slide-active {
        .df-badge__tut-box {
          opacity: 1;
        }

        .df-badge__tut-intro {
          visibility: visible;
        }
      }
    }

    &-intro {
      visibility: hidden;
      @include transition(visibility 0.1s linear);
    }

    &-title {
      margin-bottom: 12px;
      font-size: 24px;
      @extend .df-boldFF !optional;
    }

    &-intro {
      min-height: 50px;
      line-height: 1.5em;
      font-size: 14px;
      @extend .df-regularFF !optional;
    }
  }

  &__share {
    &-box {
      .df-badge__box {
        background: none;

        @media screen and (min-width: $media-mobile-max) {
          padding: 0;
        }

        @media screen and (max-width: $media-mobile-max) {
          display: block;

          &-lft {
            margin-right: 0;
            margin-bottom: 12px;
          }

          &-title,
          &-caption {
            text-align: center;
          }
        }

        .df-collector {
          .df-badge__box-thumb {
            width: 86px !important;
            height: 96px !important;
          }
        }

        &-thumb {
          width: 86px !important;
          height: 86px !important;
          z-index: 1;
        }

        &-title {
          font-size: 20px;
          margin-bottom: 0;
        }

        &-caption {
          font-size: 14px;
        }

        &-title,
        &-caption {
          color: $white;
        }

        &-lft {
          justify-content: center;
          align-items: center;
          @extend %flexbox !optional;

          @media screen and (max-width: $media-mobile-max) {
            width: 80px;
            height: 80px;
            margin-left: auto;
            margin-right: auto;
            margin-top: 20px;
          }

          &::after,
          &::before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            @include transform(translate(-50%, -50%));
            @extend .df-borRad100 !optional;
          }

          &::after {
            background: linear-gradient(
              180deg,
              #423b92 0%,
              rgba(19, 52, 125, 0) 88.85%
            );
            width: calc(100% + 20px);
            height: calc(100% + 20px);

            @media screen and (min-width: $media-mobile-max) {
              width: calc(100% + 30px);
              height: calc(100% + 30px);
            }
          }

          &::before {
            background: linear-gradient(
              180deg,
              #261f71 0%,
              rgba(19, 52, 125, 0) 88.85%
            );
            width: calc(100% + (20px * 2));
            height: calc(100% + (20px * 2));

            @media screen and (min-width: $media-mobile-max) {
              width: calc(100% + (30px * 2));
              height: calc(100% + (30px * 2));
            }
          }
        }
      }
    }

    &-thumb {
      height: 200px;
      margin: 0 auto 34px;
      background-image: url(../images/sharenative-bg-mob.png?v=#{$image-version});
      background-position: top left;
      background-repeat: no-repeat;
      justify-content: center;
      align-items: center;
      @include background-size(cover);
      @extend .df-borRad8 !optional;
      @include box-shadow(0px, 7px, 14px, 0, rgba(15, 30, 90, 0.27));
      @extend %flexbox !optional;

      @media screen and (min-width: $media-mobile-max) {
        padding-left: 130px;
        padding-right: 34px;
        justify-content: flex-start;
        background-image: url(../images/sharenative-bg-web.png?v=#{$image-version});
      }
    }

    &-native {
      ul {
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-start;
        @extend %flexbox !optional;

        li {
          cursor: pointer;
          flex-basis: calc(100% / 4);
          text-align: center;
          margin-bottom: 20px;

          @media screen and (min-width: $media-mobile-max) {
            flex-basis: calc(100% / 5);
            margin-bottom: 24px;
          }
        }
      }

      &-icon {
        width: 48px;
        height: 48px;
        margin: 0 auto 4px;

        img {
          display: block;
          width: 100%;
        }
      }

      &-title {
        font-size: 12px;
        color: $black;
        @extend .df-regularFF !optional;
      }

      .df-contest__cta {
        padding: 0;
        margin-bottom: 24px;

        ul {
          display: block;

          li {
            margin: 0;
          }
        }

        i {
          margin-right: 8px;
        }
      }
    }
  }
}

// swiper view all
