  &__box {
    margin-bottom: 20px;
    color: $white;
    justify-content: flex-start;
    align-items: flex-start;
    @extend %flexbox !optional;
  }

  &__lft {
    flex-basis: 100%;
  }

  &__rgt {
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-mobile-max) {
      width: 100px;
      height: 100px;
    }

    img {
      display: block;
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 12px;
    font-size: 14px;
