    font-size: 12px;
    text-align: center;
    color: #9b9b9b;
    @extend .df-mediumFF !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 14px;
    }
  }

  &__inputs {
    .df-contest__wrap {
      padding: 0;
      margin: 0;
      background-color: transparent;
    }

    .df-contest__box {
      box-shadow: none;
      padding: 0;
    }
  }

  &__cBox {
    margin-top: 30px;
    margin-bottom: 30px;

    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 18px;
        }
      }
    }

    .df-checkBox [type="checkbox"]:checked + label,
    .df-checkBox [type="checkbox"]:not(:checked) + label {
      padding-left: 40px;
      font-size: 12px;
      padding-right: 0;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 14px;
      }
    }

    .df-checkBox [type="checkbox"]:checked + label::after,
    .df-checkBox [type="checkbox"]:checked + label::before,
    .df-checkBox [type="checkbox"]:not(:checked) + label::after,
    .df-checkBox [type="checkbox"]:not(:checked) + label::before {
      left: 0;
      @extend .df-borRad2 !optional;
    }

    .df-checkBox [type="checkbox"]:checked + label::before,
    .df-checkBox [type="checkbox"]:not(:checked) + label::before {
      border-width: 2px;
    }

    .df-checkBox [type="checkbox"]:checked + label a,
    .df-checkBox [type="checkbox"]:not(:checked) + label a {
      color: $cta-primary-color;
      // border-bottom: 1px solid $cta-primary-color;
    }
  }

  &__login {
    padding: 16px;

    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 8px;
        }
      }
    }
  }
}

