      padding-right: 8px;
      box-shadow: 0.3px 0px 0px 0px $neutral-medium;
      .df-transfer__plyr {
        &.df-spinner,
        &.df-pacer {
          .df-transfer__plyr-name {
            padding-right: 16px;
            &::after {
              left: auto;
              right: 0;
            }
          }
        }
      }
    }
    &-rgt {
      padding-left: 8px;
      box-shadow: -0.3px 0px 0px 0px $neutral-medium;
      .df-transfer__plyr {
        &.df-spinner,
        &.df-pacer {
          .df-transfer__plyr-name {
            padding-left: 16px;
            &::after {
              left: 0;
            }
          }
        }
      }
      .df-transfer__plyr {
        flex-direction: row-reverse;
        &-dtls {
          text-align: right;
        }
      }
    }
  }

  &__disclaimer {
    font-size: 10px;
    color: $neutral-medium;
    padding: 16px 0;
    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }
  }
}
