* {
  margin: 0;
  padding: 0;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  -webkit-backface-visibility: hidden;
  @include box-sizing(border-box);

  &::-webkit-calendar-picker-indicator {
    background-image: url("data:image/png;base64,");
  }

  @media screen and (min-width: $media-mobile-max) {
    scrollbar-width: thin;
    scrollbar-color: rgba($client-primary-color, 1)
      rgba($client-primary-color, 0.3);

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: rgba($client-primary-color, 0.3);
    }

    &::-webkit-scrollbar-thumb {
      background: rgba($client-primary-color, 1);
    }

    &::-webkit-scrollbar-thumb:hover {
      background: rgba($client-primary-color, 1);
    }
  }
}

body {
  touch-action: manipulation;

  &.df-ar {
    direction: rtl;
  }
}

html,
body {
  height: 100%;
  /* Needed for container's min-height  */
}
html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;

  &:focus-within {
    scroll-behavior: smooth;
  }
}
.df-main {
  line-height: normal;
  position: relative;
  font-size: 16px;
  min-height: 100vh;
  font-family: $regular_font-family, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans",
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
    "Noto Color Emoji";
  font-weight: $regular_font-weight;
  color: $black;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  backface-visibility: hidden;
  background-color: $client-primary-color;

  @media (min-device-width: 414px) and (max-device-width: 1023px) and (orientation: landscape) {
    position: relative;
    height: 100vh;

    div {
      display: none;
    }

    &::after,
    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      @include transform(translateY(-50%));
    }

    &::after {
      content: "";
      width: 48px;
      height: 48px;
      margin: 0 auto;
      background: url(../images/rotate-your-device.svg?v=#{$image-version})
        no-repeat center center;
      @include background-size(contain);
    }

    &::before {
      content: "Rotate your Device";
      color: $white;
      text-align: center;
      font-size: 16px;
      margin-top: 44px;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }

  &__bg {
    display: none;

    @media screen and (min-width: $media-mobile-max) {
      position: fixed;
      left: 550px;
      top: 0;
      width: calc(100% - 550px);
      height: 100vh;
      background-color: $client-primary-color;
      background: url(../images/rhs-bg.png?v=#{$image-version}) no-repeat center
        center;
      justify-content: center;
      align-items: center;
      @include flexbox;
      // background: url(../images/isl-rhs-bg.png?v=#{$image-version}) no-repeat center right #2D3EA7;
      @include background-size(cover);

      // &:after {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   top: 0;
      //   width: 100%;
      //   height: 100%;
      //   background: url(../images/isl-rhs-filler-tr.png?v=#{$image-version}) no-repeat top left, url(../images/isl-rhs-filler-tr.png?v=#{$image-version}) no-repeat bottom right;
      //   // @include background-size(contain);
      //   // @include transform(translateY(-50%));
      // }

      // &::before {
      //   content: "";
      //   position: absolute;
      //   left: auto;
      //   right: 0;
      //   top: auto;
      //   bottom: 0;
      //   width: 50%;
      //   height: 50%;
      //   background: url(../images/icct20-rhs-fillers.png?v=#{$image-version}) no-repeat bottom right;
      //   @include background-size(contain);
      // }
    }

    &-box {
      // position: relative;
      z-index: 1;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
    }

    &-thumb {
      position: absolute;
      left: 40px;
      top: 40px;
      width: 120px;

      img {
        display: block;
        width: 100%;
      }
    }

    &-title {
      color: $white;
      transform: rotate(-10deg);
      line-height: 1;
      letter-spacing: 1px;

      span {
        text-align: center;
        display: block;
        font-size: 30px;
        @extend .df-blackFF--sec !optional;

        // @extend .df-regularFF !optional;
        @media screen and (min-width: $media-ex-large-max) {
          font-size: 40px;
        }

        &:not(:first-child) {
          font-size: 80px;
          @extend .df-blackFF--sec !optional;
          @media screen and (min-width: $media-ex-large-max) {
            font-size: 100px;
          }
        }
      }
    }
  }

  &__container {
    max-width: 1180px;
    width: 100%;
    position: relative;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    z-index: 1;

    @media screen and (max-width: $media-mobile-max) {
      padding: 0;
    }
  }

  &__containerFluid {
    width: 100%;
  }

  &__row {
    margin-right: -15px;
    margin-left: -15px;
  }

  &__wrap {
    padding-right: 15px;
    padding-left: 15px;
  }

  ul,
  ol {
    margin: 0;
    padding: 0;
    font-size: 0;
    list-style: none;
  }

  em {
    font-style: normal;
  }

  a {
    text-decoration: none;
  }

  b,
  strong {
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
  }

  @media screen and (-webkit-min-device-pixel-ratio: 0) {
    select:focus,
    textarea:focus,
    input:focus {
      font-size: 16px;
    }

    input:focus,
    textarea:focus {
      @include placeholder {
        font-size: 16px;
      }
    }
  }

  input:focus,
  textarea:focus {
    @include placeholder {
      color: $transparent;
      outline: none;
      box-shadow: none;
    }
  }

  input,
  textarea {
    @include placeholder {
      color: $steel;
      outline: none;
      box-shadow: none;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }

  :focus {
    outline: none;
  }

  *,
  *::before,
  *::after {
    @include box-sizing(border-box);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    outline: 0;
    -webkit-tap-highlight-color: transparent;
  }
}

//pop-ups
.df-popup {
  &__wrap {
    z-index: -9999;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 15px;
    overflow: auto;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    @extend %flexbox !optional;

    @media screen and (max-width: $media-laptop-max) {
      font-size: 14px;
      padding: 0 10px;
    }

    @media screen and (max-width: $media-mobile-max) {
      padding: 10px;
    }
  }

  &__container {
    max-height: calc(100vh - 40px);
    align-items: center;
    max-width: 650px;
    width: 100%;
    margin: 0 auto;
    padding-top: 50px;
    position: relative;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @extend %flexbox !optional;
    @include transform(translate(0, -25%) scale(0));
    @include transition(transform 0.3s ease-in);
    @include box-shadow(0px, 0px, 20px, 10px, rgba($black, 0.2));

    @media screen and (max-width: $media-laptop-max) {
      max-width: 550px;
    }

    @media screen and (max-width: $media-mobile-max) {
      max-height: calc(100vh - 140px);
    }
  }

  &__close {
    position: absolute;
    top: 0;
    left: auto;
    right: 0;
    width: 100%;
    height: 50px;
    cursor: pointer;
    z-index: 2;

    &::after {
      content: "\e901";
      font-family: "ultaFulta" !important;
      font-size: 24px;
      color: $white;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      justify-content: flex-end;
      align-items: center;
      @extend %flexbox !optional;
    }
  }

  &__head,
  &__foot,
  &__body {
    padding: 20px 20px 10px;
    width: 100%;

    @media screen and (max-width: $media-mobile-max) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__head,
  &__foot {
    position: relative;
  }

  &__head {
    padding-bottom: 0;
  }

  &__body {
    padding-top: 10px;
    padding-bottom: 20px;
    -webkit-overflow-scrolling: touch;
    flex-grow: 1;
    flex-shrink: 1;
    overflow: auto;
  }

  &__foot {
    padding-top: 0;

    @media screen and (max-width: $media-mobile-max) {
      padding-top: 10px;
    }
  }

  &__title {
    text-align: center;
    color: $black;
    font-size: 20px;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
    padding: 0 20px 15px;
    border-bottom: 1px solid rgba($client-complementary-color, 0.5);

    @media screen and (max-width: $media-mobile-max) {
      font-size: 16px;
      padding-bottom: 10px;
    }

    &-sub {
      padding-bottom: 20px;
      text-align: center;
      font-size: 16px;
      color: $client-complementary-color;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
    }
  }
}

.bodyNoScroll {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;

  @media screen and (max-width: $media-desktop-min) {
