//table style start here
.df-tbl {
  display: table;
  width: 100%;

  &__head,
  &__body,
  &__foot {
    display: table-header-group;
  }

  &__row {
    display: table-row;
  }

  &__cell {
    display: table-cell;
    vertical-align: middle;
  }
}

//shortname/fullname
.df-name {
  &__short {
    display: none;

    @media screen and (max-width: $media-mobile-max) {
      display: block;
    }
  }

  &__full {
    @media screen and (max-width: $media-mobile-max) {
      display: none;
    }
  }
}

//
.df-noDataFound {
  color: $header-text;
  font-size: 14px;
  text-align: center;
  @extend .df-regularFF !optional;
  padding-bottom: 16px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  @extend %flexbox !optional;

  @media screen and (min-width: $media-mobile-max) {
    font-size: 16px;
  }

  &__text {
    padding-bottom: 20px;
    &--big {
      font-size: 18px;
      @extend .df-boldFF !optional;
      color: $client-primary-color;

      @media screen and (min-width: $media-mobile-max) {
        font-size: 22px;
      }
    }
  }

  &__thumb {
    margin-bottom: 16px;
  }

  &__cta {
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    @extend .df-boldFF !optional;
    color: $cta-primary-color;
    padding: 20px 16px 0;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }
  }
}

//Buttons
.df-btn {
  position: relative;
  overflow: hidden;
  color: $white;
  min-width: 150px;
  height: 40px;
  text-align: center;
  margin: 0 auto;
  cursor: pointer;
  font-size: 14px;
  // letter-spacing: 0.15px;
  @extend .df-boldFF !optional;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  @extend .df-borRad100 !optional;
  @extend %flexbox !optional;

  &__link {
    cursor: default;
    text-transform: none;
    @extend .df-boldFF !optional;
    height: auto;
    color: rgba($client-primary-color, 0.6);

    em {
      cursor: pointer;
      color: $cta-primary-color;
    }
  }

  &__facebook,
  &__google {
    height: 70px;
    font-size: 20px;
    @extend .df-regularFF !optional;
    color: $white;
    padding: 0;
    position: relative;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;
    @include border-radius(0);

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0;
      @include transition(height 0.3s ease-in-out);
    }

    &:hover {
      &::after {
        height: 100%;
      }
    }

    i,
    span {
      padding: 5px;
      position: relative;
      z-index: 1;
    }

    i {
      font-size: 30px;
    }

    span {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__facebook {
    background: $facebook;

    &::after {
      background: #071c53;
    }
  }

  &__google {
    background: $googlePlus;

    &::after {
      background: #780808;
    }
  }

  &.df-disabled {
    pointer-events: none;
    cursor: default;
    background: $very-light-purple;
    color: $disabled-text;
  }

  &__primary {
    background: $cta-primary-color;
    color: $white;

    &.df-disabled {
      // background: $light-blue-grey;
      // color: $white;
      cursor: default;
    }
  }

  &__secondary {
    background: $white;
    color: $cta-primary-color;
    border: 1px solid $cta-primary-color;

    &.df-disabled {
      cursor: default;
      // background: $very-light-purple;
      border-color: $very-light-purple;
      // color: $disabled-text;
    }
  }

  &__white {
    min-width: auto;
    font-size: 14px;
    padding: 4px 12px;
    max-height: 28px;
    background-color: $white;
    color: $client-primary-color;
    justify-content: center;
    align-items: center;
    @extend %inline-flex !optional;

    &--outline {
      border: 1px solid $white;
      color: $white;
    }

    &--fill {
      background-color: $white;
      color: $client-primary-color;
    }
  }

  &__social {
    &--fb {
      color: $facebook;
    }

    &--g {
      color: #7b7a7a;
    }
  }

  &__primary,
  &__secondary,
  &__white,
  &.df-disabled {
    &--outline {
      background: transparent;
    }
  }
}

//Form lable
.df-lbl {
  margin-bottom: 4px;
  font-size: 12px;
  color: rgba($client-primary-color, 0.6);
  font-family: $regular_font-family;
  font-weight: $regular_font-weight;
}

//Form Dropdown
.df-drpDwn {
  position: relative;
  display: inline-block;
  width: 100%;

  &.active {
    .df-drpDwn__value {
      &::after {
        @include transform(translateY(-50%) rotateX(180deg));

        @media screen and (max-width: $media-mobile-max) {
          @include transform(translateY(-50%) rotateX(0deg));
        }
      }
    }

    .df-drpDwn__value {
      @include border-radius(8px 8px 0 0);
    }

    .df-drpDwn__list {
      pointer-events: auto;
      @include opacity(1);
    }
  }

  &__value {
    cursor: pointer;
    position: relative;
    padding: 10px 15px;
    padding-right: 25px;
    border: none;
    background: #f2f2f2;
    font-size: 15px;
    color: #333333;
    height: 39px;
    @extend .df-regularFF !optional;
    @include text-truncate;
    @include border-radius(8px);

    @media screen and (max-width: $media-laptop-max) {
      height: 35px;
      font-size: 13px;
      @include border-radius(6px);
    }

    @media screen and (max-width: $media-tablet-max) {
      font-size: 12px;
      @include border-radius(4px);
    }

    &::after {
      content: "\e900";
      font-family: "ultaFulta" !important;
      color: #474747;
      position: absolute;
      left: auto;
      font-size: 10px;
      right: 10px;
      top: 50%;
      @include transform(translateY(-50%));
      @include transition(transform 0.4s ease-in-out);
    }

    select {
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: none;

      @media screen and (max-width: $media-mobile-max) {
        display: block;
      }
    }
  }

  &__list {
    position: absolute;
    background-color: #f1ebe3;
    width: 100%;
    z-index: 3;
    left: 0;
    top: 100%;
    height: 200px;
    padding-bottom: 10px;
    overflow: auto;
    pointer-events: none;
    @include opacity(0);
    @include transition(all 0.2s ease-in);

    @media screen and (max-width: $media-mobile-max) {
      display: none;
    }

    &Up {
      bottom: 40px;
      top: auto;
    }

    ul {
      li {
        font-size: 14px;
        padding: 10px 25px;
        cursor: pointer;

        @media screen and (max-width: $media-laptop-max) {
          font-size: 12px;
        }

        @media screen and (max-width: $media-tablet-max) {
          padding: 8px 15px;
        }

        &:not(:last-child) {
          border-bottom: 1px solid rgba($black, 0.2);
        }
      }
    }
  }
}

//Form Input
.df-input {
  z-index: 1;
  position: relative;

  &-limit {
    font-size: 10px;
    color: $body-text;
    @extend .df-regularFF !optional;
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-mobile-max) {
      font-size: 12px;
    }

    span {
      display: block;
    }
  }

  input {
    box-shadow: none;
    outline: none;
    border: none;
    font-size: 16px;
    color: $black;
    font-family: $bold_font-family;
    font-weight: $bold_font-weight;
    padding: 10px;
    padding-right: 35px;
    height: 36px;
    width: 100%;
    display: block;
    background: #f5f6fd;
    border: none;
    @include border-radius(18px);

    @media screen and (max-width: $media-mobile-max) {
      height: 36px;
      padding: 5px;
    }
  }

  textarea {
    box-shadow: none;
    outline: none;
    border: none;
    font-size: 16px;
    width: 100%;
    background: #e5c37c;
    border: 1px solid #bea267;
    color: #584b30;
    padding: 10px;
    resize: none;
  }

  &--pwd,
  &--clear,
  &--search {
    position: relative;

    .df-input--icon {
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;

      &::after {
        font-family: "dfi" !important;
        color: $white;
        justify-content: center;
        align-items: center;
        @extend %flexbox !optional;
      }
    }
  }

  &--pwd {
    .df-input--icon {
      &::after {
        content: "\e906";
      }

      &-active {
        &::after {
          content: "\e917";
        }
      }
    }
  }

  &--clear {
    .df-input--icon {
      &::after {
        content: "\e903";
        width: 14px;
        height: 14px;
        font-size: 16px;
        color: $client-primary-color;
        // background: $light-grey-blue;
        // @extend .df-borRad100 !optional;
      }
    }
  }

  &--search {
    input {
      padding-left: 40px !important;
      padding-right: 40px !important;
    }

    &::after {
      content: "\e900";
      font-family: "dfi" !important;
      width: 16px;
      height: 16px;
      position: absolute;
      left: 12px;
      top: 50%;
      color: $client-primary-color;
      @include transform(translateY(-50%));
    }
  }

  &--icon {
    cursor: pointer;
    position: absolute;
    left: auto;
    right: 0;
    height: 100%;
    width: 35px;
    top: 0;
  }

  &--success {
    input {
      border-color: $green !important;
    }
  }

  &--danger {
    input {
      color: $red;
      border-color: $red !important;
    }
  }
}

//Checkbox button
.df-checkBox {
  position: relative;

  [type="checkbox"] {
    &:checked,
    &:not(:checked) {
      position: absolute;
      left: -9999px;

      & + label {
        position: relative;
        padding-right: 30px;
        cursor: pointer;
        line-height: normal;
        display: block;
        font-size: 14px;
        color: rgba($client-primary-color, 0.6);
        font-family: $regular_font-family;
        font-weight: $regular_font-weight;

        &::after,
        &::before {
          position: absolute;
          left: auto;
          right: 0;
          top: 50%;
          width: 20px;
          height: 20px;
          @extend .df-borRad100 !optional;
        }

        &::before {
          background: $white;
          content: "";
          border: 1px solid #dadada;
          transform-origin: center center;
          @include transform(translateY(-50%));
        }

        &::after {
          font-family: "dfi" !important;
          content: "\e90b";
          color: $client-primary-color;
          font-size: 14px;
          transform-origin: center center;
          justify-content: center;
          align-items: center;
          @include transition(all 0.2s ease);
          @extend %flexbox !optional;
        }

        a {
          font-size: 12px;
          color: $white;
          font-family: $bold_font-family;
          font-weight: $bold_font-weight;
        }
      }
    }

    &:checked {
      & + label {
        color: $client-primary-color;
      }
    }

    &:not(:checked) + label::after {
      opacity: 0;
      @include transform(scale(0) translateY(-50%));
    }

    &:checked + label::after {
      border: 2px solid $client-primary-color;
      opacity: 1;
      @include transform(scale(1) translateY(-50%));
    }

    &:checked + label::before {
      @include transition(all 0.2s ease);
    }

    &:disabled {
      :checked + label::before,
      :not(:checked) + label::before {
        border-color: #ccc;
        background-color: #eee;
      }
    }
  }

  &:disabled:checked + label::after {
    background: #aaa;
  }
}

// radio button
.df-radioBtn {
  position: relative;
  background: #e5e5e5;
  margin: 0 -16px;
  padding: 12px 16px;
  justify-content: space-between;
  align-items: center;
  @extend %flexbox !optional;

  &__wrap {
    position: relative;
    justify-content: flex-end;
    align-items: center;
    @extend %inline-flex !optional;
  }

  input {
    cursor: pointer;
    height: 100%;
    width: 100%;
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    @include transition(all 0.3s ease-in-out);

    &[type="checkbox"]:checked ~ .df-radioBtn__action {
      background-color: $light-grey-blue-two;
    }

    &[type="checkbox"]:checked ~ .df-radioBtn__action .df-radioBtn__toggle {
      left: 14px;
      background-color: $client-primary-color;
    }

    &[type="checkbox"]:checked ~ .df-radioBtn__lbl {
    }
  }

  &__lbl,
  &__action {
    padding-right: 10px;
  }

  &__action {
    padding: 0;
    position: relative;
    width: 34px;
    height: 14px;
    background-color: rgba(#969696, 0.38);
    @include border-radius(20px);
  }

  &__lbl {
    font-size: 12px;
    color: $steel;
  }

  &__toggle {
    background: $white;
    display: inline-block;
    height: 20px;
    width: 20px;
    position: absolute;
    left: 0;
    top: 50%;
    box-shadow: 0 1px 3px 0 rgba($black, 0.2), 0 2px 1px -1px rgba($black, 0.12),
      0 1px 1px 0 rgba($black, 0.14);
    @include transform(translateY(-50%));
    @extend .df-borRad100 !optional;
    @include transition(all 0.1s linear);
  }
}

.df-form {
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }

  &__floating {
    margin-bottom: 4px;
    position: relative;
    height: 60px;
    width: 100%;
    background-color: $input-bg;
    justify-content: flex-start;
    align-items: flex-end;
    @extend %flexbox !optional;

    .df-lbl {
      color: $body-text;
      padding-left: 16px;
      padding-right: 16px;
      font-size: 14px;
      position: absolute;
      z-index: 1;
      left: 0;
      top: 32px;
      @include transition(top 0.1s ease-in-out, font-size 0.1s ease-in-out);
    }

    .df-input {
      width: 100%;
      input {
        padding-left: 16px;
        padding-right: 16px;
      }
    }

    &.df-active {
      .df-lbl {
        color: $disabled-text;
        font-size: 12px;
        top: 10px;
      }

      .df-input {
        input {
          border-bottom-color: $client-primary-color;
        }
      }
    }
  }

  &__control {
    margin-bottom: 10px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

