//animations transition
.df-fade {
  &.in {
    z-index: 9999;
    background: rgba($text-dark, 0.8);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    @include opacity(1);
    @include transition(opacity 0.1s linear, padding-top 0.1s linear);

    &.df-overlay__dark {
      background: rgba($text-dark, 0.8);

      .df-overlay__body,
      .df-overlay__container {
        background-color: transparent;
      }

      .df-overlay__close {
        font-size: 10px;
        color: $white;
        left: auto;
        right: 16px;
        top: 16px;
        border: 2px solid $white;
        width: 26px;
        height: 26px;
        @extend .df-borRad100 !optional;
      }
    }

    .df-overlay__container {
      @include animation("df-popInUp .15s both");
    }

    .df-overlay__body {
      height: calc(100vh - 60px);
    }
  }

  &.out {
    @include opacity(0);
    @include transition(opacity 0.1s linear, padding-top 0.1s linear);

    .df-overlay__container {
      @include animation("df-popOutUp .15s both");
    }
  }
}

// Accordion
.df-accordion {
  margin: 12px 0;
  position: relative;

  &__box {
    background: $white;
    padding: 12px 16px;
    @extend .df-borRad8 !optional;
    box-shadow: 0 3px 6px 0 rgba($black, 0.15);

    &:not(:last-child) {
      margin-bottom: 12px;
      // border-bottom: 1px solid rgba(#dcdcdc, 0.6);
    }

    &.df-active {
      .df-accordion__head {
        padding-bottom: 12px;

        &::after {
          color: $default;
          content: "\e904";
          // @include transform(rotateX(180deg));
        }
      }

      .df-accordion__body {
        display: block;
      }
    }

    &.df-noClick {
      box-shadow: 0 2px 4px 0 rgba(1, 1, 6, 0.05);

      .df-accordion__head {
        &::after {
          display: none;
        }
      }

      .df-accordion__body {
        display: block;
      }
    }
  }

  &__head {
    cursor: pointer;
    position: relative;

    &::after {
      content: "\e906";
      font-family: "dfi" !important;
      color: $client-primary-color;
      position: absolute;
      left: auto;
      top: 0;
      right: 0;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
      @include transition(transform 0.1s ease-in-out);
    }

    &--booster {
      padding-bottom: 28px;

      &.df-accordion__head::after {
        bottom: 0;
      }
    }

    &-top {
      padding-bottom: 8px;
      margin-bottom: 8px;
      border-bottom: 1px solid #f5f5f5;

      span {
        display: inline-block;
        font-size: 14px;
        color: rgba($black, 0.6);
        @extend .df-regularFF !optional;

        &:nth-child(2) {
          color: $client-primary-color;
          @extend .df-mediumFF !optional;
          padding: 0 12px 0 4px;
        }
      }
    }

    &-bot {
      ul {
        justify-content: space-between;
        align-items: flex-start;
        @extend %flexbox !optional;

        li {
          flex-grow: 1;
          flex-basis: 100%;

          &:not(:first-child) {
            text-align: right;
          }
        }
      }
    }
  }

  &__body {
    display: none;
    border-top: 1px solid rgba(#dcdcdc, 0.6);

    .df-noDataFound {
      padding-bottom: 0;

      &__text {
        padding-bottom: 0;
      }
    }

    .df-accordion__heading {
      font-size: 14px;
      padding: 8px 16px;
      background-color: rgba($progress-color, 0.1);
    }
  }

  &__loader {
    position: relative;
    min-height: 150px;
    padding: 0 !important;

    .df-loader {
      background: transparent;
    }
  }

  &__heading {
    font-size: 16px;
    color: $client-primary-color;
    @extend .df-boldFF !optional;
  }
}

