@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap");

@font-face {
  font-family: "hurmegeometricsans2_black";
  src: url("../fonts/hurmegeometricsans2_black.eot?v=#{$cache}");
  src: url("../fonts/hurmegeometricsans2_black.eot?v=#{$cache}")
      format("embedded-opentype"),
    url("../fonts/hurmegeometricsans2_black.woff2?v=#{$cache}") format("woff2"),
    url("../fonts/hurmegeometricsans2_black.woff?v=#{$cache}") format("woff"),
    url("../fonts/hurmegeometricsans2_black.ttf?v=#{$cache}") format("truetype"),
    url("../fonts/hurmegeometricsans2_black.svg#hurmegeometricsans2_black?v=#{$cache}")
      format("svg");
}

@font-face {
  font-family: "hurmegeometricsans2_bold";
  src: url("../fonts/hurmegeometricsans2_bold.eot?v=#{$cache}");
  src: url("../fonts/hurmegeometricsans2_bold.eot?v=#{$cache}")
      format("embedded-opentype"),
    url("../fonts/hurmegeometricsans2_bold.woff2?v=#{$cache}") format("woff2"),
    url("../fonts/hurmegeometricsans2_bold.woff?v=#{$cache}") format("woff"),
    url("../fonts/hurmegeometricsans2_bold.ttf?v=#{$cache}") format("truetype"),
    url("../fonts/hurmegeometricsans2_bold.svg#hurmegeometricsans2_bold?v=#{$cache}")
      format("svg");
}

@font-face {
  font-family: "hurmegeometricsans2_semibold";
  src: url("../fonts/hurmegeometricsans2_semibold.eot?v=#{$cache}");
  src: url("../fonts/hurmegeometricsans2_semibold.eot?v=#{$cache}")
      format("embedded-opentype"),
    url("../fonts/hurmegeometricsans2_semibold.woff2?v=#{$cache}")
      format("woff2"),
    url("../fonts/hurmegeometricsans2_semibold.woff?v=#{$cache}") format("woff"),
    url("../fonts/hurmegeometricsans2_semibold.ttf?v=#{$cache}")
      format("truetype"),
    url("../fonts/hurmegeometricsans2_semibold.svg#hurmegeometricsans2_semibold?v=#{$cache}")
      format("svg");
}

